import TableBody from './TableBody.js';
import EmailTableBody from './EmailTableBody.js';
import EmailButton from './EmailButton.js';
import ManualButton from './ManualButton.js';
import TableHeaders from './TableHeaders.js';
import MainGroup from './MainGroup.js';
import React, {Component} from "react";
import ReactHtmlParser from 'html-react-parser'; 
import { CSVLink } from "react-csv";
import PropTypes from 'prop-types';
import $ from "jquery";
import Modal from "./Modal";
import EmailEditor from "./EmailEditor";

export default class NIHTable extends Component {
  state = {
    emailTable: "",
    notes: "",
    notesLoading: "",
    sendEmailAddress: "",
    sendName: "",
    showModal: false,
    message: "",
    loading: "",
  };

  static propTypes = {
    lookupResults: PropTypes.oneOfType([
      PropTypes.objectOf(
        PropTypes.objectOf(
          PropTypes.objectOf(
            PropTypes.any
          )
        )
      ),
      PropTypes.array
    ])
  };

  componentDidMount() {
    this.getEmailTableAndNotes(this.props.tableNum);
    this.loadModificationsForCSV();
  }

  loadModificationsForCSV() {
    const mods = this.props.tableDataModifications ?? {};
    let changed = false;
    for (const recordId in this.props.lookupResults) {
      for (const recordInstance in this.props.lookupResults[recordId]) {
        for (let i=0; i < this.props.tableData.length; i++) {
          const row = this.props.tableData[i];
          if (row['recordInstance'] === recordInstance) {
            for (const date in this.props.lookupResults[recordId][recordInstance]) {
              for (const col in this.props.lookupResults[recordId][recordInstance][date]) {
                const val = this.props.lookupResults[recordId][recordInstance][date][col];
                if (val !== row[col]) {
                  const rowTitle = NIHTable.makeRowTitle(row, this.props.headers, this.props.tableNum);
                  if (typeof mods[rowTitle] === "undefined") {
                    mods[rowTitle] = {};
                  }
                  mods[rowTitle][col] = val;
                  changed = true;
                }
              }
            }
          }
        }
      }
    }
    if (changed && this.props.updateModifications) {
      this.props.updateModifications(mods);
    }
  }

  getRowsFromEmail(email) {
    const emailInLower = email.toLowerCase();
    const nameHeaderIndex = 0;
    const emailIndex = 'email';
    const nameIndex = this.props.headers[nameHeaderIndex];
    const rows = [];
    for (let i=0; i < this.props.tableData.length; i++) {
      const row = this.props.tableData[i];
      const rowEmail = (typeof row[emailIndex] == "undefined") ? NIHTable.getEmailFromName(row[nameIndex].toLowerCase()) : row[emailIndex].toLowerCase();
      if (rowEmail === emailInLower) {
        rows.push(row);
      }
    }
    return rows;
  }

  getVerificationDates(link, allEmails, tableNum) {
    const query = {
      action: 'getDateOfLastVerification',
      emails: allEmails,
      tableNum: tableNum,
    };
    console.log("query: "+JSON.stringify(query));
    return new Promise((resolve, reject) => {
      const requestOptions = {
        method: 'post',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(query)
      };
      fetch(link, requestOptions)
      .then((response) => response.json())
      .then((items) => {
        console.log(JSON.stringify(items));
        if (items.error) {
          reject(items.error);
        } else {
          resolve(items);
        }
      })
      .catch((error) => {
        console.error(error);
        let mssg = "General failure";
        if (error.toString().match(/Unexpected token/)) {
            mssg = 'Unable to access database for emails. (You might need to login to REDCap again.)';
        } else {
            mssg = error+' Unable to access database for emails.';
        }
        alert(mssg);
      });
    });
  }

  makeNotesDiv(notes) {
    const rows = [];
    const headerStyle = { width: "150px" };
    const nameStyle = { width: "100px" };
    for (const name in notes) {
      for (const projectHeader in notes[name]) {
        for (const date in notes[name][projectHeader]) {
          for (const tableNum in notes[name][projectHeader][date]) {
            const note = notes[name][projectHeader][date][tableNum];
            const rowKey = projectHeader+"_"+name;
            const projectKey = rowKey+"_project";
            const nameKey = rowKey+"_name";
            const notesKey = rowKey+"_notes";
            rows.push(<tr key={rowKey}>
              <th key={projectKey} style={headerStyle}>{ReactHtmlParser(projectHeader)}</th>
              <td key={nameKey} style={nameStyle}>{name}</td>
              <td key={notesKey}>
                <strong>Table {tableNum} - {MainGroup.ymd2mdy(date)}</strong><br/>
                {ReactHtmlParser(note)}
              </td>
            </tr>)
          }
        }
      }
    }
    if (rows.length === 0) {
      const classNames = ["centered"];
      return <p className={classNames}>No notes are available for these faculty.</p>;
    }
    return <div>
      <h4>Notes from Faculty</h4>
      <table>
        <thead>
          <tr>
            <th style={headerStyle}>Project</th>
            <th style={nameStyle}>Faculty</th>
            <th>Note</th>
          </tr>
        </thead>
        <tbody>
        {rows.map(row => row)}
        </tbody>
      </table>
    </div>;
  }

  getLoadingMessage(source) {
    return this.getMessage("Loading from "+source+"...");
  }

  getMessage(mssg) {
    return <p><img src={this.getPageUrl('loading.gif')} alt='loading'/><br />{mssg}</p>;
  }

  closeModal() {
    this.setState({
      ...this.state,
      showModal: false,
    });
  }

  addCustomTable3(mssg) {
    const toReplace = '[Relevant Table 3 Rows]';
    if (this.props.tableData.length === 0) {
      return mssg.replace(toReplace, 'No table data available.');
    }
    const headers = this.props.headers;
    const tableData = this.props.csvData ?? [];

    let tableHTML = "<table><thead><tr>";
    tableHTML += '<th>'+headers.join('</th><th>')+'</th>';
    tableHTML += "</tr></thead><tbody>";
    for (let i=1; i < tableData.length; i++) {
      const row = tableData[i];
      const rowClass = (i % 2 === 1) ? "even" : "odd";
      tableHTML += '<tr>';
      for (let j=0; j < row.length; j++) {
        tableHTML += '<td class="'+rowClass+'">'+row[j]+'</td>';
      }
      tableHTML += '</tr>';
    }
    tableHTML += "</tbody></table><br/><br/>";
    return mssg.replace(toReplace, tableHTML);
  }

  sendEmail() {
    let mssg = $('.ql-editor').html();
    const email = this.state.sendEmailAddress;
    const scholarEmail = this.state.sendScholarEmailAddress;
    const fromEmail = $('#from').val();
    const subject = $('#subject').val();
    if (!fromEmail || !EmailButton.isEmail(fromEmail)) {
      alert('The from email '+fromEmail+' is invalid. Please try entering again.');
      return;
    }
    const dateOfReport = this.props.dateOfReport;
    const link = this.props.link;
    const nameToAddress = $('#emailName').val();
    const name = nameToAddress.replace(/^Delegate for /, "");
    if (this.props.tableNum === 3) {
      mssg = this.addCustomTable3(mssg);
    }
    const query = {
      action: 'sendVerificationEmail',
      email: email,
      scholarEmail: scholarEmail,
      name: name,
      mssg: mssg,
      from: fromEmail,
      subject: subject,
      dateOfReport: dateOfReport,
      savedName: this.props.savedName,
      tableNum: this.props.tableNum
    };
    this.setState({
      ...this.state,
      loading: this.getMessage("Sending..."),
      showModal: false,
    });
    return new Promise((resolve, reject) => {
      const requestOptions = {
        method: 'post',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(query)
      };
      fetch(link, requestOptions)
      .then(response => response.json())
      .then(items => {
        console.log(items['mssg']);
        this.setState({
          ...this.state,
          loading: '',
        });
        alert(items['mssg']);
      })
      .catch(error => {
        console.error(error);
        this.setState({
          ...this.state,
          loading: "",
        });
        let mssg = "General failure";
        if (error.toString().match(/Unexpected token/)) {
            mssg = 'Unable to access database to send email. (You might need to login to REDCap again.)';
        } else {
            mssg = error+' Unable to access database to send email.';
        }
        alert(mssg);
      });
    });
  }

  saveAndEnqueueEmailForTable3() {
    const email = $('#to').val();
    if (!email || !EmailButton.isEmail(email)) {
      alert('The email '+email+' is invalid. Please try entering again.');
      return;
    }
    this.props.saveTable(true)
    .then(() => {
      const name = "";
      this.setState({
        ...this.state,
        sendEmailAddress: email,
        sendName: name,
        sendScholarEmailAddress: email,
        showModal: true,
      });
    });
  }

  enqueueEmail(email, name, scholarEmail) {
    if (!email || !EmailButton.isEmail(email)) {
      alert('The email '+email+' is invalid. Please try entering again.');
      return;
    }
    this.setState({
      ...this.state,
      sendEmailAddress: email,
      sendName: name,
      showModal: true,
      sendScholarEmailAddress: scholarEmail,
    });
    alert('Please fill out the yellow form to send the email.');
  }

  static getEmailFromRow(row, nameIndex) {
    const emailIndex = 'email';
    if (typeof row[emailIndex] === "undefined") {
      return NIHTable.getEmailFromName(row[nameIndex]);
    } else {
      return row[emailIndex].toLowerCase();
    }
  }

  static getEmailFromName(nameAndEmail) {
    const matches = nameAndEmail.match(/mailto:[^'^"]+/i);
    if (matches) {
      const mailto = matches[0].toLowerCase();
      return mailto.replace(/mailto:/i, '');
    } else {
      return "";
    }
  }

  getEmailTableAndNotes(tableNum) {
    return new Promise((resolve, reject) => {
      if ((tableNum === 2) || (tableNum === 4)) {
        const emailHeaders = ["Name", "Send Email", "Manually Approve", "Date When Last Confirmed"];
        const namesAndEmails = [];
        const nameHeaderIndex = 0;
        const emailIndex = 'email';
        const nameIndex = this.props.headers[nameHeaderIndex];
        const seenEmails = [];
        const link = this.props.link;
  
        const allEmails = [];
        for (let i=0; i < this.props.tableData.length; i++) {
          const row = this.props.tableData[i];
          if (typeof row[emailIndex] === "undefined") {
            allEmails.push(NIHTable.getEmailFromName(row[nameIndex]));
          } else {
            allEmails.push(row[emailIndex].toLowerCase());
            }
        }
  
        this.setState({
          ...this.state,
          notesLoading: this.getLoadingMessage('Prior Entries'),
        });
        this.getVerificationDates(link, allEmails, tableNum)
        .then((items) => {
          const dates = items['dates'];
          const notes = items['notes'];
          for (let i=0; i < this.props.tableData.length; i++) {
            const row = this.props.tableData[i];
            const email = NIHTable.getEmailFromRow(row, nameIndex);
            const name = NIHTable.stripLinksAndText(row[nameIndex]).replace(/<br[\s\/]*>/g, "");
            const rows = this.getRowsFromEmail(email);
            let dateLastConfirmed = "Never Confirmed";
            if (dates[email]) {
              dateLastConfirmed = dates[email];
            }
            if (seenEmails.indexOf(email) < 0) {
              const emailButton = <EmailButton
                email={email}
                name={name}
                rows={rows}
                link={link}
                dateOfReport={this.props.dateOfReport}
                grantTitle={this.props.grantTitle}
                pi={this.props.pi}
                showModalForEmail={this.enqueueEmail.bind(this)}
              />;
              const manualButton = <ManualButton
                email={email}
                link={link}
              />;
              namesAndEmails.push([name, emailButton, manualButton, dateLastConfirmed, email]);
              seenEmails.push(email);
            }
          }
          const emailTable = <section>
            <h4>Send Emails to Confirm Tables 2 &amp; 4</h4>

            <table>
              <TableHeaders
                headers={emailHeaders}
                dateOfReport={this.props.dateOfReport}
              />
              <EmailTableBody
                headers={emailHeaders}
                data={namesAndEmails}
              /> 
            </table>
          </section>;
          this.setState({
            ...this.state,
            emailTable: emailTable,
            notes: this.makeNotesDiv(notes),
            notesLoading: "",
          });
          resolve(true);
        })
        .catch((error) => {
          console.error(error);
          let mssg = "General failure";
          if (error.toString().match(/Unexpected token/)) {
              mssg = 'Unable to access database for email table. (You might need to login to REDCap again.)';
          } else {
              mssg = error+' Unable to access database for email table.';
          }
          alert(mssg);
        });  
      } else if (tableNum === 3) {
        this.setState({
          ...this.state,
          emailTable: this.makeEmailTableForTable3(),
        });
        resolve(true);
      } else {
        this.setState({
          ...this.state,
          emailTable: "",
        });
        resolve(true);
      }
    });
  }

  makeEmailTableForTable3() {
    return <section>
      <h4>Send Email to Confirm Table 3</h4>
      <p>
        <input type='email' id='to' /> <button onClick={this.saveAndEnqueueEmailForTable3.bind(this)}>Save &amp; Set Up Email Message</button></p>
    </section>;
  }

  getPageUrl(localUrl) {
    const params = this.getUrlVars();
    if (params['page']) {
      const parts = [];
      parts.push("pid="+params['pid']);
      if (localUrl) {
        const page = "reporting/tables2-4/run/"+localUrl;
        const pageWithoutPHPSuffix = page.replace(/\.php$/, "");
        parts.push("page="+encodeURIComponent(pageWithoutPHPSuffix));
      } else if (params['page']) {
        parts.push("page="+encodeURIComponent(params['page']));
      }
      if (params['prefix']) {
        parts.push("prefix="+encodeURIComponent(params['prefix']));
      }
      return "?"+parts.join("&");
    }
    return localUrl;
  }

  getUrlVars() {
    const vars = {};
    window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
      vars[key] = value;
    });
    return vars;
  }
  
  static stripHTML(str, replacement) {
    if (typeof replacement == "undefined") {
      replacement = "";
    }
    return str.replace(/<[^>]+>/g, replacement);
  }

  static stripLinksAndText(str, replacement) {
    if (typeof replacement == "undefined") {
      replacement = "";
    }
    return str.replace(/<a\s.+?<\/a>/gi, replacement);
  }

  updateCSVWithModifications(rowTitle, col, val) {
    const mods = this.props.tableDataModifications ?? {};
    if (typeof mods[rowTitle] === "undefined") {
      mods[rowTitle] = {};
    }
    mods[rowTitle][col] = val;
    if (this.props.updateModifications) {
      this.props.updateModifications(mods);
    }
  }
  
  static stripTooltips(item) {
    const withoutWide = item.replace(/<span class=["']tooltip["']><span class=["']widetooltiptext["']>.+?<\/span>.+?<\/span>/i, '');
    return withoutWide.replace(/<span class=["']tooltip["']><span class=["']tooltiptext["']>.+?<\/span>.+?<\/span>/i, '');
  }

  static shrinkCommas(list) {
    while (list.match(/^, /)) {
      list = list.replace(/^, /, "");
    }
    while (list.match(/, $/)) {
      list = list.replace(/, $/, "");
    }
    while (list.match(/, , /)) {
      list = list.replace(/, , /, ", ");
    }
    return list;
  }

  static removeUncheckedRadios(html) {
    return NIHTable.removeInputsAndLabels(html, 'radio');
  }

  static removeUncheckedChecks(html) {
    return NIHTable.removeInputsAndLabels(html, 'checkbox');
  }

  static removeInputsAndLabels(html, inputType) {
    const screenRE = new RegExp('type\\s*=\\s*[\'"]'+inputType+'[\'"]', 'i');
    const selectedSyntax = 'checked';
    const selectedRE = new RegExp(' '+selectedSyntax, 'i');
    const customDepts = {};
    if (html.match(screenRE)) {
      const nodes = html.split(/</);
      const nodesWithoutInputs = [];
      const idsToHide = [];
      let i, j;
      const nodeRE = new RegExp('^input[^>]*type\\s*=\\s*[\'"]'+inputType+'[\'"]', 'i');
      for (i=0; i < nodes.length; i++) {
        const node = nodes[i];
        if (node.match(nodeRE)) {
          const inputNodes = node.split(/>/);
          const newInputNodes = [];
          for (j=0; j < inputNodes.length; j++) {
            const inputNode = inputNodes[j];
            if (!inputNode.match(/^input/i)) {
              newInputNodes.push(inputNode);
            } else if (inputNode.match(/id\s*=\s*['"][^"^']+['"]/i)) {
              const matches = inputNode.match(/id\s*=\s*['"]([^"^']+)['"]/i);
              if (matches.length >= 2) {
                const id = matches[1];
                if ((inputType === 'checkbox') && !$('#'+id).is(':'+selectedSyntax) && !inputNode.match(selectedRE)) {
                  idsToHide.push(id);
                } else if ((inputType === 'radio') && inputNode.match(/name\s*=\s*['"][^"^']+['"]/i)){
                  const nameMatches = inputNode.match(/name\s*=\s*['"]([^"^']+)['"]/i);
                  if (nameMatches.length >= 2) {
                    const name = nameMatches[1];
                    const selId = $('[name='+name+']:'+selectedSyntax).attr('id');
                    if (
                      (
                        (typeof selId === "undefined")
                        && !inputNode.match(selectedRE)
                      )
                      || (
                        (typeof selId !== "undefined")
                        && (id !== selId)
                      )
                    ) {
                      idsToHide.push(id);
                    } else if (id.match(/_other$/)) {
                      const textName = id.replace(/_other$/, '___other');
                      customDepts[name] = $('[name='+textName+']').val();
                    }
                  }
                }
              } else {
                console.log("Matches don't line up ("+JSON.stringify(matches)+") This should never happen");
              }
            }
          }   
          if (newInputNodes.length > 0) {
            const newNode = newInputNodes.join('>');
            nodesWithoutInputs.push(newNode);
          }       
        } else {
          nodesWithoutInputs.push(node);
        }
      }

      const newNodes = [];
      let openLabel = "";
      for (i=0; i < nodesWithoutInputs.length; i++) {
        const node = nodesWithoutInputs[i];
        if (node.match(/^label[^>]+for=['"][^"^']+['"]/)) {
          const matches = node.match(/^label[^>]+for=['"]([^"^']+)['"]/);
          if (matches.length >= 2) {
            openLabel = matches[1];
            if (idsToHide.indexOf(openLabel) < 0) {
              const name = openLabel.replace(/_other$/, '');
              const replacedNode = node.replace(/Other:/, customDepts[name] ?? '');
              newNodes.push(replacedNode);
            }
          } else {
            console.log("Matches don't line up ("+JSON.stringify(matches)+") This should never happen");
            newNodes.push(node);
          }
        } else if (node.match(/^\/label>/i)) {
          openLabel = "";
          newNodes.push("/label>, ");
        } else {
          newNodes.push(node);
        }
      }

      return newNodes.join("<");
    } else {
      return html;
    }
  }

  static makeRowTitle(row, headers, tableNum) {
    const keyAry = [];
    const numValues = (tableNum === 2) ? 1 : 3;
    for (let i=0; (i < headers.length) && (i < numValues); i++) {
        if (row[headers[i]]) {
            const strippedHTML = row[headers[i]].replace(/<[^>]+>/g, "");
            keyAry.push(strippedHTML);
        }
    }
    return keyAry.join("___");
  }

  getDefaultEmailText() {
    let grantIdentifyingInfo = this.props.grantTitle ? ' ('+this.props.grantTitle : '';
    if (this.props.pi && grantIdentifyingInfo) {
      grantIdentifyingInfo += ' / '+this.props.pi+')';
    } else if (this.props.pi && !grantIdentifyingInfo) {
      grantIdentifyingInfo += '('+this.props.pi+')';
    } else if (grantIdentifyingInfo) {
      grantIdentifyingInfo += ')';
    }
    let mssg = "";
    if (this.state.sendName) {
      mssg += "<p>Dear "+this.state.sendName+",</p>";
    }
    let tableNums = '';
    if ((this.props.tableNum === 2) || (this.props.tableNum === 4)) {
      tableNums = "2 & 4";
    } else if (this.props.tableNum === 3) {
      tableNums = this.props.tableNum;
    }
    mssg += "<p>A training grant you are affiliated with"+grantIdentifyingInfo+" is preparing its formal review to the NIH. Can you please verify or correct the following information we have for you?</p>";
    mssg += "<p>This information will be made part of an automated system for better reporting. Be aware that for efficiency's sake, this information will be securely stored in a database and be made accessible to others at "+this.props.mainInstitution+".</p>";
    mssg += "<p>Thanks!</p>";
    mssg += "[Relevant Table "+tableNums+" Rows]";
    return mssg;
  }

  render() {
    let emailTable = "";
    if (
      ((this.props.tableNum === 3) && (this.props.tableData.length > 0))
      || (this.props.tableNum === 2)
      || (this.props.tableNum === 4)
    ) {
      emailTable = this.state.emailTable ?? "";
    }
    const defaultEmailText = this.getDefaultEmailText();
    const notes = this.state.notes ?? "";
    const notesLoading = this.state.notesLoading ?? "";
    let caveats = "";
    if (this.props.tableNum === 2) {
      caveats = <p>Be aware that some grants count the number of trainees differently. This can result in different counts.</p>;
    }
    const csvDownloadLink = this.props.csvData ? <CSVLink data={this.props.csvData}>Download to CSV</CSVLink> : "";
    const leftClass = 'alignleft';
    const rightClass = 'alignright';
    const fullWidthClass = 'fullWidth';
    const saveBar = 'fullWidth yellowTable';
    const saveTableTD = this.props.saveTable ? <button onClick={this.props.saveTable}>Save Table</button> : <span>&nbsp;</span>;
    const longSubject = { width: '350px', };
    const defaultSubject = 'NIH Training Tables';
    return (
      <section>
          {caveats}
          <table className={fullWidthClass}>
              <TableHeaders
                headers={this.props.headers}
                dateOfReport={this.props.dateOfReport}
              />
              <TableBody
                headers={this.props.headers}
                data={this.props.tableData}
                link={this.props.link}
                tableNum={this.props.tableNum}
                lookupResults={this.props.lookupResults}
                addRow={this.props.addRow}
                deleteRow={this.props.deleteRow}
                updateCSV={this.updateCSVWithModifications.bind(this)}
                emptyTableMessage={this.props.emptyTableMessage}
              />
          </table>
          <table className={saveBar}>
            <tbody>
              <tr>
                <td className={leftClass}>{csvDownloadLink}</td>
                <td className={rightClass}>{saveTableTD}</td>
              </tr>
            </tbody>
          </table>
          {notesLoading}
          <br/><br/>
          {notes}
          <Modal
              show={this.state.showModal}
              closeMessage='Cancel'
              okMessage='Send'
              onOk={this.sendEmail.bind(this)}
              onClose={this.closeModal.bind(this)}
              title='Customize Email'
            >
              <div>
                To: {this.state.sendEmailAddress}<br/>
                From: <input type='email' id='from' /><br/>
                Subject: <input type='text' id='subject' style={longSubject} defaultValue={defaultSubject} />
                <input type='hidden' id='emailName' value={this.state.sendName} />
              </div>
              <EmailEditor
                defaultText={defaultEmailText}
              />
          </Modal>
          {this.state.loading}
          {emailTable}
          <br/><br/>
      </section>
    );
  }
}