import React, {Component} from "react";
import $ from "jquery";

export default class TooltipButton extends Component {
    transferValue() {
        const sel = this.props.selector;
        const value = this.props.value;
        $(sel).val(value);
    }

    render() {
        const paragraphKey = this.props.buttonKey+"_p";
        return (<p key={paragraphKey}><button key={this.props.buttonKey} onClick={this.transferValue.bind(this)}>Use Value</button></p>);
    }
}