
import Menu from './Menu.js';
import NIHTable from './NIHTable.js';
import React, {Component} from "react";
import $ from "jquery";
import PlainTableBody from './PlainTableBody.js';
import ReactDOMServer from 'react-dom/server';
import TableRow from './TableRow.js';
import GrantChooseTable from './GrantChooseTable.js';

const tables = [
    { "id": 2, "tableName": "Table 2"},
    { "id": 3, "tableName": "Table 3"},
    { "id": 4, "tableName": "Table 4"},
];
  
export default class MainGroup extends Component {
    state = {
      tableNumber: "",
      tableName: "",
      loading: "",
      loadingRows: "",
      dateOfReport: "",
      tableData: [],
      newTableData: [],
      headers: [],
      faculty: [],
      trainingGrants: [],
      lookupResults: {},
      matchTable: "",
      institutions: [],
      savedTableNames: {},
      currentSavedTableName: "",
      dataSource: "",
      grantTitle: "",
      grantNumber: "",
      grantPI: "",
      csvData: [],
      tableDataModifications: {},
      departments: [],
      availableGrants: [],
    };

    constructor(props) {
      super(props);
      this.handleLoad = this.handleLoad.bind(this);
    }

    componentDidMount() {
      window.addEventListener('load', this.handleLoad);
    }

    componentWillUnmount() { 
      window.removeEventListener('load', this.handleLoad);  
    }

    handleLoad() {
      const link = this.props.link;
      const query = {
        action: 'getSavedTableNames',
      };
      const requestOptions = this.makePostRequest(query);

      console.log(JSON.stringify(query));
      this.setState({...this.state, loading: this.getLoadingMessage('Saved Projects')})
      return new Promise((resolve, reject) => {
        fetch(link, requestOptions)
        .then(response => response.json())
        .then(items => {
          if (items.error) {
            this.processError(items.error);
          } else {
            this.setState({...this.state, savedTableNames: items, loading: "" });
          }
          resolve(true);
        })
        .catch(error => this.processError(error));
      });      
    }

    // Coordinated with REDCapManagement::makeHTMLId
    static makeHTMLId(id) {
      if (!id) {
        return "";
      }
      id = id.replace(/[\s-]+/g, "_");
      id = id.replace(/<[^>]+>/g, "");
      id = id.replace(/[:+"/[\]'#<>~`!@#$%\^&*()=;?.,]/g, "");
      return id;
    }

    processTable3(link) {
      const tableNum = 3;
      const {tableName} = tables.find(i => i.id === tableNum);


      this.setState({
        ...this.state,
        tableData: [],
        loading: this.getLoadingMessage("from NIH RePORTER"),
        tableNumber: tableNum,
        newTableData: [],
        dataSource: "",
        csvData: [],
        tableDataModifications: {},
      });
      const grantsQuery = {
        action: 'lookupInstitution',
        tableNum: tableNum,
        date: this.state.dateOfReport,
        savedName: this.state.currentSavedTableName ?? "",
      };
      const requestGrants = this.makePostRequest(grantsQuery);
      return new Promise((resolve, reject) => {
        const requestOptions = this.makeTableRequest(tableNum, tableName);
          fetch(link, requestOptions)
          .then(response => response.json())
          .then(items => {
            const queryItems = this.parseMainTable(items, tableName, tableNum);
            console.log("grantsQuery: "+JSON.stringify(grantsQuery));
            if (queryItems.length > 0) {
              const query2 = {
                action: 'lookup',
                tableNum: tableNum,
                queryItems: queryItems,
              };
              console.log("query2: "+JSON.stringify(query2));
              const requestOptions2 = this.makePostRequest(query2);
              return fetch(link, requestOptions2);
            } else {
              return null;
            }
          })
          .then(response => {
            if (response) {
              return response.json();
            } else {
              return [];
            }
          })
          .then(items => {
            if (Object.keys(items).length > 0) {
              if (items.error) {
                this.processError(items.error);
                reject(false);
              } else {
                console.log(JSON.stringify(items));
                this.setState({
                  ...this.state,
                  lookupResults: items,
                });
              }
            } else {
              this.setState({
                ...this.state,
                lookupResults: {},
              });
            }
            if (this.state.availableGrants.length > 0) {
              return null;
            } else {
              return fetch(link, requestGrants);
            }
          })
          .then(response => {
            if (response) {
              return response.json();
            } else if (this.state.availableGrants.length > 0) {
              return this.state.availableGrants;
            } else {
              return [];
            }
          })
          .then(items => {
            if (items.error) {
              this.processError(items.error);
              reject(false);
            } else {
              console.log(JSON.stringify(items));
              this.setState({
                ...this.state,
                newTableData: items,
                loading: "",
              });
              this.addOnClicksToCheckboxesAndRadios();
              resolve(true);
            }
          })
          .catch(error => this.processError(error));
    });
  }

  makeTableRequest(tableNum, tableName) {
      const query = {
        action: 'getTable',
        tableNum: tableNum,
        faculty: this.state.faculty,
        trainingGrants: this.state.trainingGrants,
        dateOfReport: this.state.dateOfReport,
        tableName: tableName,
      };
      if (this.state.currentSavedTableName) {
        query['savedTableName'] = this.state.currentSavedTableName;
      }
      return {
        method: 'post',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(query)
      };
    }

    makeDepartmentRadios(data) {
      const newData = [];
      const departmentHeader = 'Primary<br/>Department<br/>or Program';
      const prefix = 'department_row';
      for (let i=0; i < data.length; i++) {
        let row = data[i];
        if ((typeof row[departmentHeader] !== 'undefined') && !row[departmentHeader].match(/type\s*=\s*['"]radio['"]/i)) {
          const elems = row[departmentHeader].split(/\s*;\s*/);
          const newElems = [];
          const name = prefix+'_'+i;
          for (let j=0; j < elems.length; j++) {
            const checked = (j === 0) ? ' checked' : '';
            const elem = elems[j];
            const id = prefix+'_'+i+'_'+j;
            if (elem) {
              newElems.push('<input type="radio" name="'+name+'" value="1" id="'+id+'" '+checked+' /> <label for="'+id+'">'+elem+'</label>');
            }
          }
          const otherId = prefix+'_'+i+'_other';
          newElems.push('<input type="radio" name="'+name+'" value="1" id="'+otherId+'" /> <label for="'+otherId+'">Other:</label> <input type="text" name="'+name+'___other" />')
          row[departmentHeader] = '<div class="alignleft">'+newElems.join('<br/>')+'</div>';
        }
        newData.push(row);
      }
      return newData;
    }

    static parseEmail(nameHTML) {
      const matches = nameHTML.match(/<a href="mailto:.+".*>(.+)<\/a>/);
      if (matches) {
        return matches[1];
      } else {
        return nameHTML;
      }
    }

    // after render
    addOnClicksToCheckboxesAndRadios() {
      const link = this.props.link;
      const rowPrefix = this.getRowPrefix();
      const checkboxFunc = this.onFacultyClick;
      const radioFunc = this.onDepartmentClick;
      const thisClass = this;
      $('input[type=checkbox]').each(function(idx, ob) {
        if ($(ob).attr("onClick") === undefined) {
          const rowId = rowPrefix+$(ob).parent().parent().parent().parent().parent().find("td:eq(0) section").html();
          $(ob).on("click", checkboxFunc.bind(thisClass, rowId, link));
        }
      });
      $('input[type=radio]').each(function(idx, ob) {
        if ($(ob).attr("onClick") === undefined) {
          const email = MainGroup.parseEmail($(ob).parent().parent().parent().parent().parent().find("th:eq(0) section").html());
          $(ob).on("click", radioFunc.bind(thisClass, $(ob), email, link));
        }
      });
      $('input[type=text]').each(function(idx, ob) {
        const name = $(ob).attr('name');
        if ((typeof name !== 'undefined') && name.match(/___other$/) && ($(ob).attr("onBlur") === undefined)) {
          const email = MainGroup.parseEmail($(ob).parent().parent().parent().parent().parent().find("th:eq(0) section").html());
          $(ob).on("blur", radioFunc.bind(thisClass, $(ob), email, link));
        }
      });
    }

    // coordinated with NIHTables::getUniqueIdentifier
    static makeRecordInstance(row, headers, tableNum) {
      if (row['recordInstance']) {
        return row['recordInstance'];
      } else {
        const numValues = (tableNum === 2) ? 1 : 3;
        const values = [];
        for (var j=0; (j < headers.length) && (j < numValues); j++) {
          let id = MainGroup.makeHTMLId(row[headers[j]]);
          if (id === "") {
            id = "-";
          }
          values.push(id);
        }
        return values.join("___");
      }
    }

    parseMainTable(items, tableName, tableNum) {
      console.log(JSON.stringify(items));
      const tableData = (tableNum === 2) ? this.makeDepartmentRadios(items.data) : items.data;
      const source = items.source ?? "";
      const headers = items.headerList ?? [];
      const tableTitle = items.title ?? "";
      const combinedTitle = (tableTitle !== "") ? tableName + " - " + tableTitle : tableName;

      this.setState({
        ...this.state,
        tableName: combinedTitle,
        tableData: tableData,
        csvData: this.makeCSVFromAssocArray(tableData, headers),
        headers: headers,
        dataSource: source,
      });

      const queryItems = [];
      for (var i=0; i < tableData.length; i++) {
        const awardNo = tableData[i]['Award Number'] ?? tableData[i]['Grant Number'] ?? "";
        const record = tableData[i]['record'] ?? "";
        const recordInstance = MainGroup.makeRecordInstance(tableData[i], headers, tableNum);
        const itemHash = {record: record, recordInstance: recordInstance, awardNo: awardNo};
        if (tableNum === 2) {
          const name = tableData[i]['Name'].split(/<br\/>/)[0];
          itemHash['name'] = name;
        } else if (tableNum === 4) {
          const name = tableData[i]['Faculty Member'].split(/<br\/>/)[0];
          itemHash['name'] = name;
        }
        queryItems.push(itemHash);
      }
      console.log("queryItems: "+JSON.stringify(queryItems));
      return queryItems;
    }

    processNormalTable(link, tableNum) {
      this.setState({
        ...this.state,
        loading: this.getLoadingMessage("from All Flight Trackers"),
        tableNumber: tableNum,
        newTableData: [],
        dataSource: "",
        tableDataModifications: {},
      });
      const {tableName} = tables.find(i => i.id === tableNum);
      return new Promise((resolve, reject) => {
        const requestOptions = this.makeTableRequest(tableNum, tableName);
        console.log(JSON.stringify(requestOptions));
        fetch(link, requestOptions)
        .then(response => response.json())
        .then(items => {
          console.log(JSON.stringify(items));
          const queryItems = this.parseMainTable(items, tableName, tableNum);
          if (queryItems.length === 0) {
            return null;
          } else {
            const query2 = {
              action: 'lookup',
              tableNum: tableNum,
              queryItems: queryItems,
            };
            console.log("query2: "+JSON.stringify(query2));
            const requestOptions2 = this.makePostRequest(query2);
            return fetch(link, requestOptions2);
          }
        })
        .then(response => {
          if (response) {
            return response.json();
          } else {
            return null;
          }
        })
        .then(items => {
          if (items !== null) {
            console.log(JSON.stringify(items));
            this.setState({
              ...this.state,
              lookupResults: items,
              loading: "",
            });
          } else {
            this.setState({
              ...this.state,
              lookupResults: {},
              loading: "",
            });
          }
          this.addOnClicksToCheckboxesAndRadios();
          resolve(true);
        })
        .catch(error => this.processError(error));
      });
    }

    processError(error) {
      console.error(error);
      this.setState({
        ...this.state,
        loading: "",
      });
      let mssg = "General failure";
      if (error.toString().match(/Unexpected token/)) {
          mssg = 'Unable to access database to get data. (You might need to login to REDCap again, or you might have run out of processing time.)';
      } else {
          mssg = error+' Unable to access database to get data.';
      }
      alert(mssg);
    }

    changeTable(tableNum, link) {
      if (this.state.loading !== "") {
        return;
      }

      if (tableNum === 3) {
        this.processTable3(link)
      } else {
        this.processNormalTable(link, tableNum);
      }
    }

    removeBlanks(ary) {
      const newAry = [];
      for (var i=0; i < ary.length; i++) {
        if (ary[i] !== "") {
          newAry.push(ary[i]);
        }
      }
      return newAry;
    }

    ymd2text(ymd) {
      const nodes = ymd.split(/[-/.]/);
      if (nodes.length === 3) {
        const monthsOfYear = [
          "January",
          "February",
          "March",
          "April",
          "May",
          "June",
          "July",
          "August",
          "September",
          "October",
          "November",
          "December",
        ];
        const year = nodes[0];
        const monthIdx = nodes[1] - 1;
        const day = nodes[2].replace(/^0/, "");
        const month = monthsOfYear[monthIdx];
        return month+" "+day+", "+year;
      }
      return ymd;
    }

    getTodayYMD() {
      const dateObj = new Date();
      const month = dateObj.getMonth() + 1;
      const day = String(dateObj.getDate()).padStart(2, '0');
      const year = dateObj.getFullYear();
      return year + '-'+ month + '-' + day;
    }

    getLoadingMessage(source) {
      const srcUrl = this.getPageUrl("loading.gif");
      return <p><img src={srcUrl} alt='loading'/><br />Loading {source}...</p>;
    }

    addGrant(link) {
      const awardNo = $('#grant').val();
      if (awardNo) {
        const query = {
          action: "lookupRePORTER",
          awardNo: awardNo,
          date: this.state.dateOfReport,
        };
        console.log("query: "+JSON.stringify(query));
        const requestOptions = this.makePostRequest(query);
        this.setState({
          ...this.state,
          loadingRows: this.getLoadingMessage("from RePORTER"),
        })
        fetch(link, requestOptions)
        .then(response => response.json())
        .then(items => {
          console.log(JSON.stringify(items));
          this.setState({
            ...this.state,
            newTableData: this.state.newTableData.concat(items),
            loadingRows: "",
          });
        })
        .catch(error => {
          console.error(error);
          this.setState({
            ...this.state,
            newTableData: [],
            loadingRows: "",
          });
          let mssg = "General failure";
          if (error.toString().match(/Unexpected token/)) {
              mssg = 'Unable to access database to search for grant. (You might need to login to REDCap again.)';
          } else {
              mssg = error+' Unable to access database to search for grant.';
          }
          alert(mssg);
        })
      }
    }

    loadProjectInfo(link, name) {
      this.setState({
        ...this.state,
        loading: this.getLoadingMessage("Project Information"),
      });
      const query = {
        action: "getProjectInfo",
        name: name,
      };
      const requestOptions = this.makePostRequest(query);
      console.log(JSON.stringify(query));
      return new Promise((resolve, reject) => {
        fetch(link, requestOptions)
        .then(response => response.json())
        .then(items => {
          if (items.error) {
            this.processError(items.error);
          } else {
            console.log(JSON.stringify(items));
            this.setState({
              ...this.state,
              faculty: items.faculty,
              dateOfReport: items.date,
              grantPI: items.grantPI ?? "",
              grantTitle: items.grantTitle ?? "",
              grantNumber: items.grantNumber ?? "",
              loading: "",
            });
            resolve(true);
          }
        })
        .catch(error => this.processError(error));
      });
    }

    saveLists() {
      const selectedName = $('#savedTableName option:selected').val();
      const link = this.props.link;
      if (selectedName !== '') {
        this.loadProjectInfo(link, selectedName)
        .then(() => this.findInstitutions(link, selectedName) )
        .then(() => this.findMatches(link, this.state.faculty, this.state.dateOfReport, this.state.grantPI, this.state.grantTitle, this.state.grantNumber));
      } else {
        // new
        const tableName = $('#newSavedTableName').val();
        if (tableName === '') {
          alert("Please specify a name to save your work under!");
          return;
        }
        const facultyNames = $('textarea#facultyList').val();
        if (facultyNames === '') {
          alert("Please supply some faculty names!");
          return;
        }
        const facultyList = this.removeBlanks(facultyNames.split(/[\n\r]/));
        const dateOfReport = $('input[type=date]#dateOfReport').val();
        const grantPI = $('#grantPI').val();
        const grantTitle = $('#grantTitle').val();
        const grantNumber = $('#grantNumber').val();
        this.findInstitutions(link, tableName)
        .then(() => this.findMatches(link, facultyList, dateOfReport, grantPI, grantTitle, grantNumber));
      }
    }

    makeMatchTable(data) {
      if (data.length === 0) {
        return "";
      }
      const title = "matches";
      const facultyNames = this.state.faculty.join("\n");
      const hideStyle = {display: 'none'};
      return <section>
        <table>
          <PlainTableBody
            data={data}
            title={title}
          />
        </table>
        <p id='editFaculty' style={hideStyle}>
          <textarea id='editFacultyList' defaultValue={facultyNames} /><br/>
          <button onClick={this.changeFacultyList.bind(this)}>Search Faculty Matches Again</button>
        </p>
        <p><button onClick={this.showEditFacultyList.bind(this)}>Edit Faculty</button></p>
      </section>
    }

    showEditFacultyList() {
      $("#editFaculty").show();
    }

    changeFacultyList() {
      const facultyNames = $('#editFacultyList').val();
      if (facultyNames === '') {
        alert("Please supply some faculty names!");
        return;
      }
      const facultyList = this.removeBlanks(facultyNames.split(/[\n\r]/));
      this.findMatches(this.props.link, facultyList, this.state.dateOfReport, this.state.grantPI, this.state.grantTitle, this.state.grantNumber);
    }

    findInstitutions(link, savedTableName) {
      this.setState({
        ...this.state,
        currentSavedTableName: savedTableName,
        loading: this.getLoadingMessage("Institutions"),
      });
      const query = {
        action: "getInstitutions",
      };
      const requestOptions = this.makePostRequest(query);
      console.log(JSON.stringify(query));
      return new Promise((resolve, reject) => {
        fetch(link, requestOptions)
        .then(response => response.json())
        .then(items => {
          console.log(JSON.stringify(items));
          if (items.error) {
            this.processError(items.error);
          } else {
            this.setState({
              ...this.state,
              institutions: items,
              loading: "",
            });
            resolve(true);
          }
        })
        .catch(error => this.processError(error));
      });
    }

    findMatches(link, facultyList, dateOfReport, grantPI, grantTitle, grantNumber) {
      this.setState({
        ...this.state,
        faculty: facultyList,
        loading: this.getLoadingMessage("Matches to Flight Trackers"),
      });
      const query = {
        action: "getMatches",
        faculty: facultyList,
        date: dateOfReport,
      };
      const requestOptions = this.makePostRequest(query);
      console.log(JSON.stringify(query));
      return new Promise((resolve, reject) => {
        fetch(link, requestOptions)
        .then(response => response.json())
        .then(items => {
          console.log(JSON.stringify(items));
          $('#editFaculty').hide();
          if (items.error) {
            console.log("error");
            this.processError(items.error);
            reject();
          } else {
            const matchTable = this.makeMatchTable(items.matches);
            const departments = items['departments'] ?? [];

            this.setState({
              ...this.state,
              loading: "",
              matchTable: matchTable,
              faculty: facultyList,
              dateOfReport: dateOfReport ? dateOfReport : this.getTodayYMD(),
              grantTitle: grantTitle,
              grantNumber: grantNumber,
              grantPI: grantPI,
              departments: departments,
            });
            resolve(true);
          }
        })
        .catch(error => this.processError(error));
      });
    }

    findNumUnique(data, col) {
      const uniqueItems = [];
      for (let i=0; i < data.length; i++) {
        if (data[i][col]) {
          const val = data[i][col];
          if (uniqueItems.indexOf(val) < 0) {
            uniqueItems.push(val);
          }
        }
      }
      return uniqueItems.length;
    }

    static ymd2mdy(date) {
      const nodes = date.split(/[-/]/);
      if (nodes.length === 3) {
        return nodes[1]+"-"+nodes[2]+"-"+nodes[0];
      }
      return date;
    }

    makeQueryHeader(tableNum, numRequestedItems, numUniqueItems, loadingText) {
      const facultyTables = [2, 4];
      if (facultyTables.indexOf(tableNum) >= 0) {
        let foundText = numUniqueItems+" Preceptors Found Flight Tracker";
        if (loadingText) {
          foundText = "Searching...";
        }
        return <h3>{numRequestedItems} Preceptors Requested; {foundText}</h3>;
      } else if (tableNum === 3) {
        const grantNounFound = numUniqueItems === 1 ? "Grant" : "Grants";
        let foundText = numUniqueItems+" "+grantNounFound+" Found in Flight Tracker";
        if (loadingText) {
          foundText = "Searching...";
        }
        let requestedText = numRequestedItems+" Preceptors Requested";
        return <h3>{requestedText}; {foundText}</h3>;
      }
      return "";
    }

    fillInBlanksForTable3(row, headers) {
      const rowLength = Object.keys(row).length;
      for (let i = rowLength; i < headers.length - 1; i++) {
        row[headers[i]] = MainGroup.getNotAvailableText();
      }
      const lastHeader = headers[headers.length - 1];
      const rowId = this.getRowPrefix()+row[headers[1]];
      row[lastHeader] = this.makeFacultyChecklist(this.state.faculty, rowId);
      return row;
    }

    getRowPrefix() {
      return "row____";
    }

    makeFacultyChecklist(faculty, rowId) {
      const htmlLines = [];
      const divClass = 'left-align';
      const link = this.props.link;
      for (let i=0; i < faculty.length; i++) {
        const name = faculty[i];
        const id = rowId+this.getCheckSeparator()+MainGroup.makeHTMLId(name);
        const checkboxKey = id+"___checkbox";
        const labelKey = id+"___label";
        const divKey = id+"___div";
        htmlLines.push(<div key={divKey} className={divClass}>
          <input type='checkbox' key={checkboxKey} person={name} name={id} id={id} onClick={this.onFacultyClick.bind(this, rowId, link)} />
          &nbsp;
          <label key={labelKey} htmlFor={id}>{name}</label>
        </div>);
      }

      const otherMatches = rowId+"___matches";
      const overlapFromOthersDiv = <div key={otherMatches} id={otherMatches}></div>
      return (<section>
        {htmlLines.map(checkbox => checkbox)}
        {overlapFromOthersDiv}
      </section>);
    }

    getCheckSeparator() {
      return "____";
    }

    makeCSVFromAssocArray(assocAry, headers) {
      const modifications = this.state.tableDataModifications;
      const rows = [];
      const headerRow = [];
      for (let j=0; j < headers.length; j++) {
        const headerWithoutHTML = NIHTable.stripHTML(headers[j], " ");
        headerRow.push(headerWithoutHTML);
      }
      rows.push(headerRow);
      const checkboxColRE = /Names of.+Overlapping.+Faculty/;
      const radioColRE = /Primary.+Department.+or Program/;
  
      for (let i=0; i < assocAry.length; i++) {
        const row = assocAry[i];
        const rowTitle = NIHTable.makeRowTitle(row, headers, this.state.tableNumber);
        const newRow = [];
        for (let j=0; j < headers.length; j++) {
          const col = headers[j];
          const modCol = TableRow.makeHTMLId(col);
          let item = row[col] ?? "";
          if (typeof modifications[rowTitle] !== "undefined") {
            if (typeof modifications[rowTitle][modCol] !== "undefined") {
              item = modifications[rowTitle][modCol];
            } else if (typeof modifications[rowTitle][col] !== "undefined") {
              item = modifications[rowTitle][col];
            }
          }
          const itemAsHTML = (item.toString() === "[object Object]") ? ReactDOMServer.renderToStaticMarkup(item) : item.toString();
          const uncheckedGone = col.match(checkboxColRE) ? NIHTable.removeUncheckedChecks(itemAsHTML) : itemAsHTML;
          const unselectedGone = col.match(radioColRE) ? NIHTable.removeUncheckedRadios(uncheckedGone) : uncheckedGone;
          const itemWithoutTooltip = NIHTable.stripTooltips(unselectedGone);
          const itemWithoutLinkedText = NIHTable.stripLinksAndText(itemWithoutTooltip, "");
          const itemWithoutHTML = NIHTable.stripHTML(itemWithoutLinkedText, "");
          const shrinkCommas = NIHTable.shrinkCommas(itemWithoutHTML);
          const trimmedItem = shrinkCommas.trim();
          if ((trimmedItem === "") && !col.match(checkboxColRE)) {
            newRow.push(NIHTable.stripHTML(MainGroup.getNotAvailableText()));
          } else {
            newRow.push(trimmedItem);
          }
        }
        if (newRow.length > 0) {
          rows.push(newRow);
        }
      }
      return rows;
    }

    onDepartmentClick(jqueryObj, email, link) {
      const rowObj = $('tr[email="'+email+'"]');
      const pid = rowObj.attr("pid");
      const record = rowObj.attr("record");
      const recordInstance = rowObj.attr("recordinstance"); // all lowercase per React convention
      const col = MainGroup.makeHTMLId(this.state.headers[3]);
      let val = jqueryObj.val();
      const jqueryObjName = jqueryObj.attr("name");
      if (jqueryObj.attr("id") === jqueryObjName+"_other") {
        // get value of text
        val = $('[name='+jqueryObjName+'___other]').val();
      }
      const query = {
        action: "savePeople",
        pid: pid,
        record: record,
        recordInstance: recordInstance,
        column: col,
        value: val,
        tableNum: this.state.tableNumber,
        dateOfReport: this.state.dateOfReport,
      };
      console.log(JSON.stringify(query));
      const requestOptions = this.makePostRequest(query);
      fetch(link, requestOptions)
      .then(response => response.json())
      .then(items => {
        console.log(JSON.stringify(items));
        this.setState({
          ...this.state,
          csvData: this.makeCSVFromAssocArray(this.state.tableData, this.state.headers),
        });
      })
      .catch(error => this.processError(error));
    }
    
    onFacultyClick(rowId, link) {
      const awardNo = rowId.replace(this.getRowPrefix(), "");
      const regexStr = rowId+this.getCheckSeparator();
      const re = new RegExp(regexStr);
      const checkedNames = [];
      const uncheckedNames = [];
      $('input[type=checkbox]').each( (idx, ob) => {
        const name = $(ob).attr('name');
        if (re.exec(name)) {
          const person = $(ob).attr('person');
          if ($(ob).is(":checked")) {
              checkedNames.push(person);
          } else {
              uncheckedNames.push(person);
          }
        }
      });

      const overlapQuery = {
        action: 'saveOverlappingFaculty',
        checkedNames: checkedNames,
        uncheckedNames: uncheckedNames,
        date: this.state.dateOfReport,
        award: awardNo,
      };
      const saveOverlappingFacultyRequest = {
        method: 'post',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(overlapQuery)
      };
      fetch(link, saveOverlappingFacultyRequest)
      .then(response => response.json())
      .then(items => {
        console.log(JSON.stringify(items));
        this.setState({
          ...this.state,
          csvData: this.makeCSVFromAssocArray(this.state.tableData, this.state.headers),
        });
      })
      .catch(error => this.processError(error));
    }

    hasButton(cell) {
      const html = cell.toString();
      return html.match(/<button/i);
    }

    deleteProject() {
      const idToDelete = $('#savedTableName option:selected').val();
      const newSavedTableNames = this.state.savedTableNames;
      if (typeof newSavedTableNames[idToDelete] !== "undefined") {
        delete newSavedTableNames[idToDelete];
        const link = this.props.link;
        const query = {
          action: 'setSavedTableNames',
          savedTableNames: newSavedTableNames,
        };
        const requestOptions = this.makePostRequest(query);
  
        console.log(JSON.stringify(query));
        this.setState({...this.state, loading: this.getLoadingMessage('Updated Saved Projects')})
        return new Promise((resolve, reject) => {
          fetch(link, requestOptions)
          .then(response => response.json())
          .then(items => {
            if (items.error) {
              this.processError(items.error);
            } else {
              $('.newGrantInfo').show();
              this.setState({...this.state, savedTableNames: items, loading: "" });
            }
            resolve(true);
          })
          .catch(error => this.processError(error));
        });  
      }
    }

    deleteRow(row) {
      if (Object.keys(row).length === 0) {
        return;
      }
      if (this.state.tableNumber === 3) {
        row = this.removeBlanksForTable3(row);
      }

      const headers = this.state.headers;
      const tableData = [];
      for (let i=0; i < this.state.tableData.length; i++) {
        const currRow = this.state.tableData[i];
        let same = true;
        for (let j=0; j < headers.length; j++) {
          const header = headers[j];
          const newTableCell = currRow[header];
          const rowCell = row[header];
          if (this.cellsDifferent(newTableCell, rowCell)) {
            same = false;
            break;
          }
        }
        if (!same) {
          tableData.push(currRow);
        } else {
          console.log("Skipping "+JSON.stringify(currRow));
        }
      }

      // not const but let because unshift overwrites the entire array - I think
      let newTableData = this.state.newTableData;
      newTableData.unshift(row);

      this.setState({
        ...this.state,
        newTableData: newTableData,
        tableData: tableData,
        csvData: this.makeCSVFromAssocArray(tableData, this.state.headers),
      });
    }

    removeBlanksForTable3(row) {
      const newRow = [];
      for (const key in row) {
        const value = row[key];
        const html = value.toString();
        if (!html.match(/Not Available/) && !key.match(/Names of<br\/>Overlapping<br\/>Faculty/)) {
          newRow[key] = value;
        }
      }
      return newRow;
    }

    cellsDifferent(cell1, cell2) {
      return (
        (typeof cell1 != 'undefined')
        && (typeof cell2 != 'undefined')
        && (cell1 !== cell2)
        && !this.hasButton(cell1)
        && !this.hasButton(cell2)
      );
    }

    changeToBaseAwardNos(row) {
      if (typeof row['Award Number'] == "undefined") {
        return row;
      }
      const awardNo = row['Award Number'];
      const matches = awardNo.match(/^\d?([A-Z][A-Z0-9]{2}[A-Z]{2}\d{6})/i);
      if (matches && (matches.length > 1)) {
        const baseAwardNo = matches[1];
        row['Award Number'] = baseAwardNo;
      }
      return row;
    }

    saveAndAddRow(row) {
      if (Object.keys(row).length === 0) {
        return;
      }
      if (this.state.tableNumber === 3) {
        row = this.fillInBlanksForTable3(row, this.state.headers);
      }
      const link = this.props.link;

      const tableData = this.state.tableData;
      const headers = this.state.headers;
      tableData.push(this.changeToBaseAwardNos(row));
      const newTableData = [];
      for (let i=0; i < this.state.newTableData.length; i++) {
        let same = true;
        for (let j=0; j < headers.length; j++) {
          const header = headers[j];
          const newTableCell = this.state.newTableData[i][header];
          const rowCell = row[header];
          if (this.cellsDifferent(newTableCell, rowCell)) {
            same = false;
            break;    // inner
          }
        }
        if (!same) {
          newTableData.push(this.state.newTableData[i]);
        }
      }
      this.setState({
        ...this.state,
        newTableData: newTableData,
        tableData: tableData,
        csvData: this.makeCSVFromAssocArray(tableData, this.state.headers),
      });
      if (this.state.tableNumber === 3) {
        const awards = this.getColumn(tableData, "Award Number");
        this.checkOverlappingFaculty(awards, link);
      }
    }

    getColumn(data, col) {
      const values = [];
      for (let i=0; i < data.length; i++) {
        const row = data[i];
        if (row[col]) {
          values.push(row[col]);
        }
      }
      return values;
    }

    getPid(url) {
      const parts = url.split(/\?/);
      if (parts.length >= 2) {
        const params = parts[1].split(/&/);
        for (let i=0; i < params.length; i++) {
          const pair = params[i].split(/=/);
          if ((pair.length >= 2) && (pair[0] === "pid")) {
            return pair[1];
          }
        }
      }
      return "";
    }

    checkOverlappingFaculty(awards, link) {
      const overlapQuery = {
        action: 'lookupOverlappingFaculty',
        awards: awards,
      };
      const requestOptions = this.makePostRequest(overlapQuery);
      const currPid = this.getPid(link);
      fetch(link, requestOptions)
      .then(response => response.json())
      .then(items => {
        console.log(JSON.stringify(items));
        if (items.error) {
          this.processError(items.error);
        } else {
          if (items[currPid]) {
            for (const awardNo in items[currPid]) {
              const dates = this.sortDatesAsc(Object.keys(items[currPid][awardNo]));
              for (let i = 0; i < dates.length; i++) {
                const date = dates[i];
                const checkedNames = items[currPid][awardNo][date]['checkedNames'];
                const uncheckedNames = items[currPid][awardNo][date]['uncheckedNames'];
                const allNames = checkedNames.concat(uncheckedNames);
                const rowId = this.getRowPrefix()+awardNo;
                for (let j=0; j < allNames.length; j++) {
                  const name = allNames[j];
                  const id = rowId+this.getCheckSeparator()+MainGroup.makeHTMLId(name);
                  if ($('#'+id).length > 0) {
                    if (checkedNames.indexOf(name) >= 0) {
                      $('#'+id).attr('checked', true);
                    } else if (uncheckedNames.indexOf(name) >= 0) {
                      $('#'+id).attr('checked', false);
                    }
                  }
                }
              }
            }
          }
          const overlappingTooltipsById = {};
          for (const pid in items) {
            if (pid !== currPid) {
              for (const awardNo in items[pid]) {
                const overlappingItemsForAward = [];
                const rowId = this.getRowPrefix()+awardNo;
                const id = rowId+"___matches";
                const divKey = rowId+"___div_matches_"+pid;
                const dates = this.sortDatesAsc(Object.keys(items[currPid][awardNo]));
                for (let i = 0; i < dates.length; i++) {
                  const date = dates[i];
                  const checkedNames = items[currPid][awardNo][date]['checkedNames'];
                  const item = date+": "+checkedNames.join(", ");
                  overlappingItemsForAward.push(item);
                }
                if (overlappingItemsForAward.length > 0) {
                  const overlapInfo = overlappingItemsForAward.join(<br/>);
                  const item = <div key={divKey}>
                    <span class='tooltip'>pid: {pid}
                      <span class='widetooltiptext'>{overlapInfo}</span>
                    </span>
                  </div>;
                  if (typeof overlappingTooltipsById[id] === "undefined") {
                    overlappingTooltipsById[id] = [];
                  }
                  overlappingTooltipsById[id].push(item);
                }
              }
            }
          }
  
          const ids = Object.keys(overlappingTooltipsById);
          for (let i = 0; i < ids.length; i++) {
            const id = ids[i];
            const comps = <section>
              {overlappingTooltipsById[id].map(item => item)}
            </section>;
            const html = comps.toString();
            $('#'+id).html(html);
          }
        }
      })
      .catch(error => this.processError(error));
    }

    sortDatesAsc(dates) {
      dates.sort((a, b) => { return (new Date(a) - new Date(b)); });
      return dates;
    }

    saveAndAddAllRows() {
      const tableData = this.state.tableData;
      for (let i=0; i < this.state.newTableData.length; i++) {
        tableData.push(this.state.newTableData[i]);
      }
      this.setState({
        ...this.state,
        newTableData: [],
        tableData: tableData,
        csvData: this.makeCSVFromAssocArray(tableData, this.state.headers),
      });
    }

    resetTables() {
      this.setState({
        ...this.state,
        headers: [],
        tableData: [],
        newTableData: [],
        tableNumber: "",
        lookupResults: {},
        dataSource: "",
        csvData: [],
      });
    }

    resetPage() {
      this.setState({
        ...this.state,
        headers: [],
        tableData: [],
        newTableData: [],
        tableNumber: "",
        lookupResults: {},
        faculty: [],
        csvData: [],
      });
    }

    getLookupResults() {
      if (this.state.loading !== "") {
        return "";
      }
      if (this.state.newTableData.length > 0) {
        const newHeaders = [];
        const row0 = this.state.newTableData[0];
        for (const header in row0) {
          newHeaders.push(header);
        }
        const emptyTableMssg = "No data are available.";
        return (<section>
          <h3>{this.state.newTableData.length} Rows of Current Grants from RePORTER</h3>
          <NIHTable
            tableNum='lookup'
            link={this.props.link}
            tableData={this.state.newTableData}
            headers={newHeaders}
            addRow={this.saveAndAddRow.bind(this)}
            dateOfReport={this.state.dateOfReport}
            emptyTableMessage={emptyTableMssg}
          />
        </section>);
      }
      return "";
    }

    getLookupParagraph() {
      if (this.state.loading !== "") {
        return "";
      }
      if (this.state.tableNumber === 3) {
        return <p>
          Add Award Number (Partial or Complete) <input type='text' id='grant' defaultValue='' />
          <button onClick={this.addGrant.bind(this, this.props.link)}>Search for Grant</button>
        </p>;
      }
      return "";
    }

    getTableMetadata() {
      const numRequested = this.state.faculty.length;
      if (this.state.tableNumber === 2) {
        return {
          columnName: "Name",
          numRequested: numRequested,
        };
      } else if (this.state.tableNumber === 3) {
        return {
          columnName: "Award Number",
          numRequested: numRequested,
        };
      } else if (this.state.tableNumber === 4) {
        return {
          columnName: "Faculty Member",
          numRequested: numRequested,
        };
      } else {
        return {
          columnName: "",
          numRequested: 0,
        };
      }
    }

    getPageUrl(localUrl) {
      const params = this.getUrlVars();
      if (params['page']) {
        const parts = [];
        parts.push("pid="+params['pid']);
        if (localUrl) {
          const page = "reporting/tables2-4/run/"+localUrl;
          const pageWithoutPHPSuffix = page.replace(/\.php$/, "");
          parts.push("page="+encodeURIComponent(pageWithoutPHPSuffix));
        } else if (params['page']) {
          parts.push("page="+encodeURIComponent(params['page']));
        }
        if (params['prefix']) {
          parts.push("prefix="+encodeURIComponent(params['prefix']));
        }
        return "?"+parts.join("&");
      }
      return localUrl;
    }

    getUrlVars() {
      const vars = {};
      window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
      });
      return vars;
    }
    
    makeMainHeader() {
      const savedName = this.state.currentSavedTableName ?? "";
      const margins = {marginTop: "25px", marginBottom: "25px"};
      const projectTitle = (!savedName) ? "" : <p className='nomargin'>
      <strong>{savedName}</strong>
      &nbsp;
      <span id='status'></span>
    </p>;

      return (<section style={margins}>
        <h1 className='nomargin'>NIH Training Tables 2-4</h1>
        {projectTitle}
      </section>);
    }

    makeInstitutionList() {
      const institutions = this.state.institutions;
      if (institutions.length === 0) {
        return "";
      }
      const list = "Searching for Institutions: "+institutions.join(", ");
      const style = { margin: "0 auto 1em auto" };
      const classNames = ["smaller"];
      return <p className={classNames} style={style}>{list}</p>
    }

    makeDateNotification(date) {
      const classList = "nomargin";
      return <p className={classList}>Date to Submit Report: <strong>{this.ymd2text(date)}</strong></p>;
    }

    changeTableName() {
      const projectName = $('#savedTableName option:selected').val();
      $('#grantTitle').val('');
      $('#grantNumber').val('');
      $('#grantPI').val('');
      this.checkValidityForBlueBox();
      if (projectName === '') {
        $('#rightFiller').hide();
        $('.newGrantInfo').show();
      } else {
        $('.newGrantInfo').hide();
        $('#rightFiller').show();
        const loadingMssg = 'Loading...';
        $('.mirror').html(loadingMssg);

        const link = this.props.link;
        const query = {
          action: 'getProjectInfo',
          name: projectName
        };
        console.log(JSON.stringify(query));
        const requestOptions = this.makePostRequest(query);

        return new Promise((resolve, reject) => {
          fetch(link, requestOptions)
          .then(response => response.json())
          .then(data => {
            const unspecified = MainGroup.getUnspecifiedText();
            const pi = data.grantPI ? data.grantPI : unspecified;
            const grantTitle = data.grantTitle ? data.grantTitle : unspecified;
            const grantNumber = data.grantNumber ? data.grantNumber : unspecified;
            this.setState({
              ...this.state,
              grantPI: pi,
              grantTitle: grantTitle,
              grantNumber: grantNumber,
            });
            console.log(JSON.stringify(data));
            $('#mirrorOfFacultyList').html(data['faculty'].join("<br/>"));
            $('#mirrorOfDateOfReport').html(MainGroup.ymd2mdy(data['date']));
            $('#mirrorOfPI').html(pi);
            $('#grantPI').val(pi);
            $('#mirrorOfGrantTitle').html(grantTitle);
            $('#grantTitle').val(grantTitle);
            $('#mirrorOfGrantNumber').html(grantNumber);
            $('#grantNumber').val(grantNumber);
            this.checkValidityForBlueBox();
          })
          .catch(error => this.processError(error));
        });
      }
    }

    static getUnspecifiedText() {
      return "Unspecified";
    }

    makePostRequest(query) {
      return {
        method: 'post',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(query)
      };
    }

    updateModifications(mods) {
      console.log("updateModifications "+JSON.stringify(mods));

      const modifiedTableData = [];
      for (let i=0; i < this.state.tableData.length; i++) {
        const row = this.state.tableData[i];
        const rowTitle = NIHTable.makeRowTitle(row, this.state.headers, this.state.tableNumber);
        if (typeof mods[rowTitle] !== "undefined") {
          for (let j=0; j < this.state.headers.length; j++) {
            const header = this.state.headers[j];
            if (typeof mods[rowTitle][header] !== "undefined") {
              const alternateValue = mods[rowTitle][header];
              if (alternateValue) {
                const originalValueAsHTML = (row[header].toString() === "[object Object]") ? ReactDOMServer.renderToStaticMarkup(row[header]) : row[header].toString();
                const regex = new RegExp(alternateValue+"<br/>");
                if (!originalValueAsHTML.match(regex)) {
                  row[header] = <span>
                      {alternateValue}<br/>
                      {row[header]}
                  </span>;
                }
              }
            }
          }
        }
        modifiedTableData.push(row);
      }

      this.setState({
        ...this.state,
        csvData: this.makeCSVFromAssocArray(this.state.tableData, this.state.headers),
        tableDataModifications: mods,
        tableData: modifiedTableData,
      });
    }

    tableDataToHTML(tableData, headers, modifications) {
      const newTableData = [];
      for (let i=0; i < tableData.length; i++) {
        const row = tableData[i];
        const rowTitle = NIHTable.makeRowTitle(row, headers, this.state.tableNumber);
        const newRow = {};
        for (let j=0; j < headers.length; j++) {
          const col = headers[j];
          const modCol = TableRow.makeHTMLId(col);
          let item = row[col] ?? "";
          if (typeof modifications[rowTitle] !== "undefined") {
            if (typeof modifications[rowTitle][modCol] !== "undefined") {
              item = modifications[rowTitle][modCol];
            } else if (typeof modifications[rowTitle][col] !== "undefined") {
              item = modifications[rowTitle][col];
            }
          }
          const itemAsHTML = (item.toString() === "[object Object]") ? ReactDOMServer.renderToStaticMarkup(item) : item.toString();
          const itemWithChecks = this.changeInputTypes(itemAsHTML, 'checkbox', 'checked');
          const itemWithTextValues = this.changeTextInputs(itemWithChecks, col);
          newRow[col] = itemWithTextValues;
        }

        newRow['pid'] = row['pid'];
        newRow['record'] = row['record'];
        newRow['recordInstance'] = MainGroup.makeRecordInstance(row, headers, this.state.tableNumber);

        newTableData.push(newRow);
      }
      return newTableData;
    }

    static getNotAvailableText() {
      return "<span class='action_required'>Not Available</span>";
    }

    changeTextInputs(html, col) {
      if (html === MainGroup.getNotAvailableText()) {
        const id = MainGroup.makeHTMLId(col);
        console.log(id+" "+html);
      }
      return html;
    }

    changeInputTypes(html, inputType, mark) {
      let newHTML = html;
      $('input[type='+inputType+']:'+mark).each(function(idx, ob) {
        const id = $(ob).attr('id');
        const alreadyDoneRE = new RegExp('\\sid\\s*=\\s*[\'"]'+id+'[\'"] '+mark);
        const replaceRE = new RegExp('\\sid\\s*=\\s*[\'"]'+id+'[\'"]');
        if (newHTML.match(replaceRE) && !newHTML.match(alreadyDoneRE)) {
          newHTML = newHTML.replace(replaceRE, ' id="'+id+'" '+mark);
        }
      });
      $('input[type='+inputType+']:not(:'+mark+')').each(function(idx, ob) {
        const id = $(ob).attr('id');
        const replaceRE = new RegExp('\\sid\\s*=\\s*[\'"]'+id+'[\'"] '+mark, 'gi');
        while (newHTML.match(replaceRE)) {
          newHTML = newHTML.replace(replaceRE, ' id="'+id+'"');
        }
      });
      return newHTML;
    }

    saveTable(hideAlert)  {
      const tableNum = this.state.tableNumber;
      console.log('saveTable '+tableNum);
      const savedName = this.state.currentSavedTableName;
      const link = this.props.link;
      if (tableNum && savedName && link) {
        const query = {
          action: 'saveTable',
          tableNum: tableNum,
          name: savedName,
          tableData: this.tableDataToHTML(this.state.tableData, this.state.headers, this.state.tableDataModifications),
          facultyList: this.state.faculty,
          date: this.state.dateOfReport,
          grantPI: this.state.grantPI,
          grantTitle: this.state.grantTitle,
          grantNumber: this.state.grantNumber,
        };
        console.log(JSON.stringify(query));
        const requestOptions = this.makePostRequest(query);

        return new Promise((resolve, reject) => {
          fetch(link, requestOptions)
          .then(response => response.json())
          .then(message => {
            const mssg = message.Result;
            console.log(mssg);
            if ((typeof hideAlert !== 'boolean') || (hideAlert !== true)) {
              alert(mssg);
            }
            resolve(true);
          })
          .catch(error => this.processError(error));
        }); 
      } else {
        alert('Please choose a table to view before saving.');
      }
      return false;
    }

    canShowButton() {
      if (this.state.currentSavedTableName === '') {
        if (
          ($('#savedTableName').length > 0)
          && ($('#newSavedTableName').length > 0)
          && ($('#facultyList').length > 0)
          && ($('#dateOfReport').length > 0)
          && ($('#grantPI').length > 0)
          && ($('#grantTitle').length > 0)
          && ($('#grantNumber').length > 0)
        ) {
          const savedTableName = $('#savedTableName').val();
          if (savedTableName !== '') {
            return true;
          } else {
            const newName = $('#newSavedTableName').val();
            const facultyList = $('#facultyList').val();
            const dateOfReport = $('#dateOfReport').val();
            const grantTitle = $('#grantTitle').val();
            const grantNumber = $('#grantNumber').val();
            const grantPI = $('#grantPI').val();
            const someGrantInfo = (grantTitle !== '') || (grantNumber !== '') || (grantPI !== '');
            return (newName !== '') && (facultyList !== '') && (dateOfReport !== '') && someGrantInfo;
          }
        } else {
          return false;
        }
      }
      return false;
    }

    checkValidityForBlueBox() {
      if (this.canShowButton()) {
        $('#bigBlueButton').fadeIn(300);
      } else {
        $('#bigBlueButton').hide();
      }
    }

    makeGrantChooseTable() {
      const headers = [
        "Grant Title",
        "Award Number",
        "Project<br/>Period",
        "PD/PI",
      ];

      const unspecified = MainGroup.getUnspecifiedText();
      const grantTitle = this.state.grantTitle ? this.state.grantTitle : unspecified;
      const grantNumber = this.state.grantNumber ? this.state.grantNumber : unspecified;
      const grantPI = this.state.grantPI ? this.state.grantPI : unspecified;

      return (<section>
        <p>
          <strong>Grant Title</strong>: {grantTitle}<br/>
          <strong>Grant Number</strong>: {grantNumber}<br/>
          <strong>PI</strong>: {grantPI}
        </p>
        <GrantChooseTable
          tableData={this.state.availableGrants}
          headers={headers}
          onSelect={this.onChooseGrant.bind(this)}
          dateOfReport={this.state.dateOfReport}
        />
      </section>);
    }

    onChooseGrant(grantTitle, awardNo, pi) {
      console.log("onChooseGrant "+grantTitle+" "+awardNo+" "+pi);
      $('#grantNumber').val(awardNo);
      $('#grantPI').val(pi);
      $('#grantTitle').val(grantTitle);
      this.setState({
        ...this.state,
        grantTitle: grantTitle,
        grantNumber: awardNo,
        grantPI: pi,
      });
      this.checkValidityForBlueBox();
    }

    onChangeDate() {
      const date = $('#dateOfReport').val();
      if (!date.match(/^2\d\d\d-\d\d-\d\d$/)) {
        return;
      }

      this.checkValidityForBlueBox.bind(this);
      const grantsQuery = {
        action: 'lookupInstitution',
        tableNum: 3,
        date: date,
        savedName: "",
      };
      const requestGrants = this.makePostRequest(grantsQuery);
      const link = this.props.link;

      console.log(JSON.stringify(grantsQuery));
      this.setState({...this.state, loading: this.getLoadingMessage('Institutional Grants from NIH RePORTER')});
      return new Promise((resolve, reject) => {
        fetch(link, requestGrants)
        .then(response => response.json())
        .then(items => {
          console.log(JSON.stringify(items));
          if (items.error) {
            this.processError(items.error);
            reject();
          } else {
            this.setState({
              ...this.state,
              loading: "",
              availableGrants: items,
            });
            resolve(true);
          }
        })
        .catch(error => this.processError(error));
      });      
    }

    onChangeManual() {
      $('#grantPI').val($('#grantPIManual').val());
      $('#grantNumber').val($('#grantNumberManual').val());
      $('#grantTitle').val($('#grantTitleManual').val());
      this.checkValidityForBlueBox();
    }

    render() {
      const mainHeader = this.makeMainHeader();      
      const lookupResults = this.getLookupResults();
      const lookupParagraph = this.getLookupParagraph();
      const dateNotification = (<section>
        {this.makeDateNotification(this.state.dateOfReport)}
        {this.makeInstitutionList()}
      </section>);
      const allTableNames = Object.keys(this.state.savedTableNames).sort();
      const emptyTableMssg = (this.state.newTableData.length > 0) ? "Please add data from the table below." : "No data available.";
      const hidden = { display: 'none' };
      const savedName = this.state.currentSavedTableName ?? "";
      const source = this.state.dataSource ? '('+this.state.dataSource+')' : "";
      $('#status').html(source);
      
      if (!savedName) {
        const leftDiv = {float: 'left', width: 'calc(60% - 40px)', padding: '20px 20px 100px 20px'};
        const rightDiv = {float: 'right', width: 'calc(40% - 40px)', padding: '20px 20px 100px 20px'};
        const fillerStyle = { ...hidden, ...rightDiv, textAlign: 'center' };
        const buttonStyle = this.canShowButton() ? {} : hidden;
        const h2Blue = 'blue';
        const h2Yellow = 'yellow';
        const newGrantInfo = 'newGrantInfo';
        const maxWidth = 'max-width-1000';
        const smallMargins = 'noBottomMargin';

        const manualSpecification = (this.state.loading === "") ? <section>
          <h2 className={h2Blue}>What Identifies This Grant?</h2>
          <p><label htmlFor='grantPIManual'>PI(s):</label> <input type='text' id='grantPIManual' onChange={this.onChangeManual.bind(this)} /></p>
          <p><label htmlFor='grantTitleManual'>Grant Title:</label> <input type='text' id='grantTitleManual' onChange={this.onChangeManual.bind(this)} /></p>
          <p><label htmlFor='grantNumberManual'>Grant Number:</label> <input type='text' id='grantNumberManual' onChange={this.onChangeManual.bind(this)} /></p>
        </section> : "";
        const specifyGrant = (this.state.availableGrants.length > 0) ? this.makeGrantChooseTable() : manualSpecification;
        const deleteProject = ($('#savedTableName option:selected').val() !== "") ? <p><button onClick={this.deleteProject.bind(this)}>Delete Project</button></p> : "";

        return (
          <main className={maxWidth}>
            {mainHeader}
            <p>This widget tries to coordinate communication at an institution about NIH Training Tables 2-4. It accesses all Flight Trackers at an institution and contacts the NIH RePORTER. Please enter the following information. A blue button will pop up when you are ready to proceed.</p>
            {this.state.loading}
            <div style={leftDiv}>
              <h2 className={h2Blue}>What Project Do You Want To Use?</h2>
                <p>Load Project: <select autoFocus id='savedTableName' onChange={this.changeTableName.bind(this)} defaultValue={this.state.currentSavedTableName}>
                  <option key='___New___' value=''>---NEW---</option>
                  {allTableNames.map(tableName => <option key={tableName} value={tableName}>{tableName}</option>)}
                </select></p>
                {deleteProject}
                <div id='bigBlueButton' style={buttonStyle} onClick={this.saveLists.bind(this)}><h1>View Tables</h1></div>
              <div className={newGrantInfo}>
                <p>
                  New Name:&nbsp;
                  <input id='newSavedTableName' type='text' onChange={this.checkValidityForBlueBox.bind(this)} />
                </p>
                <h2 className={h2Yellow}>What Date Will These Tables be Submitted?</h2>
                  <p><input type='date' id='dateOfReport' onChange={this.onChangeDate.bind(this)} /></p>
                  {specifyGrant}
              </div>
            </div>
            <div style={rightDiv} className={newGrantInfo}>
              <h2 className={h2Yellow}>What Faculty are on Your Grant?</h2>
                <p>(One name per line please.)</p>
                <p><textarea id='facultyList' onChange={this.checkValidityForBlueBox.bind(this)}></textarea></p>
            </div>
            <div style={fillerStyle} id='rightFiller'>
            <input type='hidden' id='grantTitle' defaultValue={this.state.grantTitle} />
            <input type='hidden' id='grantNumber' defaultValue={this.state.grantNumber} />
            <input type='hidden' id='grantPI' defaultValue={this.state.grantPI} />
              <h4 className={smallMargins}>Date of Report</h4>
              <p id='mirrorOfDateOfReport' className='mirror'></p>
              <br/>
              <h4 className={smallMargins}>Faculty List</h4>
              <p id='mirrorOfFacultyList' className='mirror'></p>
              <br/>
              <h4 className={smallMargins}>PI(s)</h4>
              <p id='mirrorOfPI' className='mirror'></p>
              <br/>
              <h4 className={smallMargins}>Grant Number</h4>
              <p id='mirrorOfGrantNumber' className='mirror'></p>
              <br/>
              <h4 className={smallMargins}>Grant Title</h4>
              <p id='mirrorOfGrantTitle' className='mirror'></p>
              <br/>
              <br/><br/>
            </div>
          </main>
        );
      } else if ((this.state.tableData.length === 0) && (this.state.headers.length === 0)) {
        return (
          <main>
            {mainHeader}
            <Menu
              tables={tables}
              resetTables={this.resetTables.bind(this)}
              resetPage={this.resetPage.bind(this)}
              changeTable={this.changeTable.bind(this)}
              saveTable={this.saveTable.bind(this)}
              link={this.props.link}
            />
            <h3>{this.state.faculty.length} Preceptors Requested. Click a Table to Load.</h3>
            {dateNotification}
            {this.state.loading}
            {this.state.matchTable}
            {lookupResults}
            {this.state.loadingRows}
            {lookupParagraph}
          </main>
        );
      } else {
        const {columnName, numRequested} = this.getTableMetadata();
        const numFound = this.findNumUnique(this.state.tableData, columnName);
        const queryInfo = this.makeQueryHeader(this.state.tableNumber, numRequested, numFound, this.state.loading);
        const mainInstitution = (this.state.institutions.length >= 1) ? this.state.institutions[0] : "[INSTITUTION]";
        const nihTable = (<NIHTable
          lookupResults={this.state.lookupResults}
          tableNum={this.state.tableNumber}
          link={this.props.link}
          tableData={this.state.tableData}
          headers={this.state.headers}
          csvData={this.state.csvData}
          dateOfReport={this.state.dateOfReport}
          deleteRow={this.deleteRow.bind(this)}
          saveTable={this.saveTable.bind(this)}
          savedName={savedName}
          emptyTableMessage={emptyTableMssg}
          grantTitle={this.state.grantTitle}
          grantNumber={this.state.grantNumber}
          pi={this.state.grantPI}
          mainInstitution={mainInstitution}
          updateModifications={this.updateModifications.bind(this)}
          tableDataModifications={this.state.tableDataModifications}
        />);
        const mainDisplay = this.state.loading ? this.state.loading : <section>{nihTable}{lookupResults}{this.state.loadingRows}{lookupParagraph}</section>
        const mainTitle = this.state.loading ? "" : <h2>{this.state.tableName}</h2>;

        return (
          <main>
            {mainHeader}
            <Menu
              tables={tables}
              resetTables={this.resetTables.bind(this)}
              resetPage={this.resetPage.bind(this)}
              changeTable={this.changeTable.bind(this)}
              saveTable={this.saveTable.bind(this)}
              link={this.props.link}
            />
            {mainTitle}
            {queryInfo}
            {dateNotification}
            {mainDisplay}
          </main>
        );
      }
    }
}
