import React, {Component} from "react";
import ReactHtmlParser from 'html-react-parser'; 
import MainGroup from './MainGroup.js';

export default class TableHeaders extends Component {
    
    render() {
        const dateOfReport = MainGroup.ymd2mdy(this.props.dateOfReport);
        const dateSpan = "<br/><span class='smaller'>(by "+dateOfReport+")</span>";
        const headerClasses = ["greybg"];
        return (
            <thead>
                <tr>
                    {this.props.headers.map(header => {
                        const updatedHeader = header.replace(/Graduated/, "Graduated"+dateSpan)
                            .replace(/Completed<br\/>Training/, "Completed<br/>Training"+dateSpan);
                        return <th className={headerClasses} key={header}>{ReactHtmlParser(updatedHeader)}</th>;
                    })}
                </tr>
            </thead>
        );
    }
}