import React, {Component} from "react";


export default class Menu extends Component {
  constructor() {
    super();
    this.onClick = this.onClick.bind(this);
  }

  matchesKey = "matchesMenu";
  frontKey = "frontPage";
  saveKey = "saveMenu";

  onClick(id) {
    const {link} = this.props;
    if (id === this.matchesKey) {
      this.props.resetTables(id, link);
    } else if (id === this.frontKey) {
      this.props.resetPage(id, link);
    } else if (id === this.saveKey) {
      this.props.saveTable();
    } else {
      this.props.changeTable(id, link);
    }
  }

  render() {
    return (
      <nav>
        <ul>
          <li key={this.matchesKey} onClick={this.onClick.bind(null, this.matchesKey)}>Matches</li>
          {this.props.tables.map(i => (
            <li key={i.id} onClick={this.onClick.bind(null, i.id)}>{i.tableName}</li>
          ))}
          <li key={this.saveKey} onClick={this.onClick.bind(null, this.saveKey)}>Save Table</li>
        </ul>
      </nav>
      );
  }
}

