import React, {Component} from "react";
import EmailButton from './EmailButton.js';

export default class EmailTableRow extends Component {
    render() {
        const name = this.props.row[0];
        const emailButton = this.props.row[1];
        const manualButton = this.props.row[2];
        const lastConfirmedDate = this.props.row[3];
        const email = this.props.row[4];
        const style = { minWidth: '150px', maxWidth: '300px' };
        const nameKey = name+"_name";
        const emailKey = name+"_email";
        const confirmationKey = name+"_last_confirmed";
        const rowKey = name+"_row";
        const manualKey = name+"_manualconfirm";
        const emailWithMailto = EmailButton.makeMailto(email);
        const nameWithEmail = emailWithMailto ? <span>
            {name}<br/>
            {emailWithMailto}
        </span> : name;
        return (
            <tr key={rowKey}>
                <td key={nameKey}>{nameWithEmail}</td>
                <td key={emailKey} style={style}>{emailButton}</td>
                <td key={manualKey} style={style}>{manualButton}</td>
                <td key={confirmationKey} style={style}>{lastConfirmedDate}</td>
            </tr>
        );
    }
}