import React, {Component} from "react";


export default class ManualButton extends Component {
  state = {
    loading: "",
  };

  onClick() {
    const email = this.props.email;
    const link = this.props.link;
    const query = {
      action: 'manuallyApprove',
      email: email,
      tables: [2, 4],
    }
    this.setState({
      ...this.state,
      loading: "Approving..."
    });
    return new Promise((resolve, reject) => {
      const requestOptions = {
        method: 'post',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(query)
      };
      fetch(link, requestOptions)
      .then(response => response.json())
      .then(items => {
        console.log(items['mssg']);
        this.setState({
          ...this.state,
          loading: items['mssg'],
        });
      })
      .catch(error => {
        console.error(error);
        this.setState({
          ...this.state,
          loading: "",
        });
        let mssg = "General failure";
        if (error.toString().match(/Unexpected token/)) {
            mssg = 'Unable to access database to send email. (You might need to login to REDCap again.)';
        } else {
            mssg = error+' Unable to access database to send email.';
        }
        alert(mssg);
      });
    });
  }

  render() {
    const name = this.props.name;
    const paragraphKey = name+"_paragraph";
    const loadingKey = name+"_loading";
    const buttonKey = name+"_button";

    let loadingInfo = "";
    const paragraphClasses = ['nomargin'];
    if (this.state.loading) {
      loadingInfo = <p key={loadingKey} className={paragraphClasses}>{this.state.loading}</p>;
    }
    return (
      <section>
        <p key={paragraphKey} className={paragraphClasses}>
          <button key={buttonKey} onClick={this.onClick.bind(this)}>Manually Approve</button>
        </p>
        {loadingInfo}
      </section>
    );
  }
}

