import React, {Component} from "react";
import ReactHtmlParser from 'html-react-parser'; 

export default class PlainTableBody extends Component {
    render() {
        const data = this.props.data;
        const title = this.props.title;
        const tBodyKey = title+"___tbody";
        return(<tbody key={tBodyKey}>
            {data.map((row, rowIdx) => {
                const rowKey = title+"___"+rowIdx+"___row";
                return <tr key={rowKey}>
                    {row.map((item, cellIdx) => {
                        const cellKey = title+"___"+rowIdx+"___"+cellIdx;
                        if (typeof item === "string") {
                            return <td key={cellKey}>{ReactHtmlParser(item)}</td>;
                        } else {
                            return <td key={cellKey}>{item}</td>;
                        }
                    })}
                </tr>;
            })}
        </tbody>);
    }
}
