import EmailTableRow from './EmailTableRow.js';
import React, {Component} from "react";

export default class EmailTableBody extends Component {
    render() {
        const headers = this.props.headers;
        const tableData = this.props.data;
        const emailTBody = "emailTBody";
        if (tableData.length === 0) {
            const numCols = headers.length;
            const noDataKey = "noData";
            const noDataRow = "noDataRow";
            return (
                <tbody key={emailTBody}>
                    <tr key={noDataRow}>
                        <td key={noDataKey} colSpan={numCols}>No data available.</td>
                    </tr>
                </tbody>
            );
        } else {
            return (
                <tbody key={emailTBody}>
                {tableData.map(row => {
                    const rowKey = row[0]+"_emailTableRow";
                    return <EmailTableRow
                        key={rowKey}
                        row={row}
                    />
                })}
                </tbody>
            );
        }
    }
}