import React, {Component} from "react";
import ReactHtmlParser from 'html-react-parser';
import { Parser, ProcessNodeDefinitions } from "html-to-react";
import $ from "jquery";
import { renderToString } from 'react-dom/server'
import NIHTable from "./NIHTable";
import MainGroup from "./MainGroup";
import TooltipButton from "./TooltipButton";

const customElements = {
    "tooltipButton": TooltipButton
};
const htmlParser = new Parser(React);
const processNodeDefinitions = new ProcessNodeDefinitions(React);
function isValidNode() {
  return true;
}  

export default class TableRow extends Component {
    state = {
        loading: false,
    };

    getClassName(val) {
        const isString = (typeof val == "string") || (val instanceof String);
        if (isString && val.match(/\$[\d,]+(?:\.\d{2})?/)) {
            return "alignright";
        }
        return "";
    }

    getPid(url) {
        const nodes = url.split(/\?/);
        if (nodes.length >= 2) {
            const paramNodes = nodes[1].split(/&/);
            const params = {};
            for (let i=0; i < paramNodes.length; i++) {
                const n = paramNodes[i].split(/=/);
                if (n.length > 2) {
                    const nVals = [];
                    for (let j=1; j < n.length; j++) {
                        nVals.push(n[j]);
                    }
                    params[n[0]] = nVals.join('=');
                } else if (n.length === 2) {
                    params[n[0]] = n[1];
                } else if (n.length === 1) {
                    params[n[0]] = true;
                }
            }
            if (typeof params['pid'] !== "undefined") {
                return params['pid'];
            }
        }
        return '';
    }

    static makeHTMLId(str) {
        return MainGroup.makeHTMLId(str);
    }

    static addButtonsToTooltips(html, fieldSel) {
        if (html.match(/tooltip/)) {
            const splitText = "<span class='tooltip'><span class='widetooltiptext'>";
            const re = new RegExp(splitText);
            const nodes = html.split(re);
            return (<section>
                {nodes.map((itemHTML) => {
                    const itemNodes = itemHTML.split(/<\/span>/);
                    const values = (itemNodes.length > 2) ? itemNodes[1].replace(/\s\(\d+-\d+-\d{4}\)/, '').split(/<br\/>/) : [];
                    const value = (values.length > 1) ? values[0] : "";
                    const addButton = (value === "") ? "" : "<tooltipButton></tooltipButton>";
                    const fullHTML = splitText+addButton+itemHTML;
                    const buttonKey = fieldSel.replace(/[#\.]/, 'buttonKey_');
                    const sectionKey = fieldSel.replace(/[#\.]/, 'sectionKey_');
                    const processingInstructions = [
                        {
                          shouldProcessNode: (node) => {
                            return (node.name && customElements[node.name]);
                          },
                          processNode: (node) => {
                            let CustomElement = customElements[node.name];
                            return <CustomElement selector={fieldSel} value={value} buttonKey={buttonKey} />;
                          }
                        },
                        // Default processing
                        {
                          shouldProcessNode: () => true,
                          processNode: processNodeDefinitions.processDefaultNode
                        }
                      ];
                    const itemComp = htmlParser.parseWithInstructions(fullHTML, isValidNode, processingInstructions);
                    return <section key={sectionKey}>{itemComp}</section>;
                })}
            </section>)
        }
        return ReactHtmlParser(html);
    }

    addInputs(reactComp, colName, pid, recordId, recordInstance, lookupResults, awardNo) {
        const colNameNoHTML = colName.replace(/<br[\s\/]*>/gi, ' ');
        const html = renderToString(reactComp);
        const textToMatch = "Not Available";
        const regEx = new RegExp(textToMatch);
        const editableCols = [
            'Training Role',
            'Research Interest',
            'Pre-doctorates In Training',
            'Pre-doctorates Graduated',
            'Predoctorates Continued in Research or Related Careers',
            'Post-doctorates In Training',
            'Post-doctorates Completed Training',
            'Postdoctorates Continued in Research or Related Careers',
            'Number of Predoctoral Positions',
            'Number of Postdoctoral Positions',
            'Number of Short-Term Positions',
            'Number of Participating Faculty (Number Overlapping)',
            'Current Year Direct Costs',
        ];
        if (html.match(regEx) || (editableCols.indexOf(colNameNoHTML) >= 0)) {
            const recordResults = (lookupResults && lookupResults[recordId]) ? lookupResults[recordId] : [];
            const myResultsByDate = recordResults[recordInstance] ?? [];
            const colWithoutHTML = TableRow.makeHTMLId(colName);
            const mypid = ((pid !== '') && !isNaN(pid)) ? pid : this.getPid(this.props.link);
            const sep = "____";
            let field = colWithoutHTML+sep+mypid+sep+recordId+sep+recordInstance;
            if (this.props.tableNum === 3) {
                field += sep+awardNo;
            }
            let defaultValue = "";
            let latestDate = "";
            for (const date in myResultsByDate) {
                const value = myResultsByDate[date][colName] ?? "";
                if (value && !latestDate) {
                    defaultValue = value;
                    latestDate = date;
                } else if (value && latestDate) {
                    const latestDateObj = new Date(latestDate);
                    const newDateObj = new Date(date);
                    if (latestDateObj.getTime() < newDateObj.getTime()) {
                        latestDate = date;
                        defaultValue = value;
                    }
                }
            }
            
            const inputStyle = { width: '100px' };
            const compHideStyle = { display: 'none' };
            const compClasses = 'action_required';
            const newInputComp = <input type='text' style={inputStyle} id={field} onBlur={this.saveField.bind(this, field)} defaultValue={defaultValue} />;
            if (html.match(/tooltip/)) {
                const naText = MainGroup.getNotAvailableText();
                const postNAHTML = html.replace(naText, '');
                const postNAComp = TableRow.addButtonsToTooltips(postNAHTML, '#'+field);
                return (<section>
                    {ReactHtmlParser(naText)}
                    {newInputComp}
                    {postNAComp}
                </section>);
            } else {
                const textComp = defaultValue ? <span className={compClasses} style={compHideStyle}>{textToMatch}</span> : reactComp;
                return <section>
                    {textComp}
                    {newInputComp}
                </section>;
            }
        } else {
            return reactComp;
        }
    }

    filterOutMetadata(ary, headers) {
        const newAry = [];
        for (let i=0; i < ary.length; i++) {
            if (headers.indexOf(ary[i]) >= 0) {
                newAry.push(ary[i]);
            }
        }
        return newAry;
    }

    saveField(field) {
        const val = $('#'+field).val();
        const link = this.props.link;
        const nodes = field.split(/____/);
        if (nodes.length >= 4) {
            const col = nodes[0];
            const pid = nodes[1];
            const record = (nodes[2] !== "undefined") ? nodes[2] : "";
            const recordInstance = (nodes[3] !== "undefined") ? nodes[3] : "";
            const query = {
                action: "savePeople",
                pid: pid,
                record: record,
                recordInstance: recordInstance,
                column: col,
                value: val,
                tableNum: this.props.tableNum,
                dateOfReport: this.props.dateOfReport,
            };
            if (nodes.length === 5) {
                const awardNo = nodes[4];
                query['awardNo'] = awardNo;
            }
            const requestOptions = {
                method: 'post',
                headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify(query)
            };
            console.log(JSON.stringify(query));
            this.setState({
                ...this.state,
                loading: true,
            });
            const parentTR = $('#'+field).parents("tr");
            const parentTD = $('#'+field).parents("td");
            if (parentTR) {
                parentTD.find('input[type=text]').attr("readonly", "readonly");
            }
            fetch(link, requestOptions)
            .then(response => response.json())
            .then((items) => {
                console.log(JSON.stringify(items));
                this.refreshScreen(parentTR);
                this.props.updateCSV(this.props.title, col, val);
                if (parentTD) {
                    if (val) {
                        parentTD.find('.action_required').hide();
                    } else {
                        parentTD.find('.action_required').show();
                    }
                }
            })
            .catch(error => {
                this.refreshScreen(parentTR);
                this.props.updateCSV(this.props.title, col, val);
                console.error(error);
                if (error.toString().match(/Unexpected token/)) {
                    const mssg = 'Unable to access database to save field. (You might need to login to REDCap again.)';
                    alert(mssg);
                } else {
                    const mssg = error+' Unable to access database to save field.';
                    alert(mssg);
                }
            });
        }
    }

    makeReact(item) {
        if ((typeof item == "string") || (item instanceof String)) {
            return <section>{ReactHtmlParser(item)}</section>;
        }
        return item;
    }

    refreshScreen(parentTR) {
        this.setState({
            ...this.state,
            loading: false,
        });
        if (parentTR) {
            parentTR.find('td input[type=text]').attr("readonly", false);
        }
    }

    render() {
        const loading = this.state.loading ?? false;
        let row = this.props.row ?? [];
        const headers = this.props.headers ?? [];
        const keys = this.filterOutMetadata(Object.keys(row), headers);
        keys.sort(function(a, b) {
            for (let i=0; i < headers.length; i++) {
                if (headers[i] === a) {
                    return true;
                } else if (headers[i] === b) {
                    return false;
                }
            }
            return true;
        });
        let deleteRowX = "";
        if ((this.props.tableNum === 3) && this.props.deleteRow) {
            deleteRowX = <button onClick={this.props.deleteRow.bind(null, row)}>X</button>;
        }
        const classNames = [];
        if (loading) {
            classNames.push("greyedOut")
        }
        if (keys.length === 1) {
            const firstItem = keys.shift();
            return (
                <tr key={this.props.title} email={row['email']} record={row['record']} pid={row['pid']} recordinstance={row['recordInstance']} >
                    <td colSpan={headers.length} key={this.props.title+"___"+firstItem}>{row[firstItem]}</td>
                </tr>
            );
        } else if (keys.length === 2) {
            const firstItem = keys.shift();
            const secondItem = keys.shift();
            const numCols = headers.length - 1;
            return (
                <tr key={this.props.title} email={row['email']} record={row['record']} pid={row['pid']} recordinstance={row['recordInstance']} >
                    <th key={this.props.title+"___"+firstItem}>{row[firstItem]} {deleteRowX}</th>
                    <td colSpan={numCols} key={this.props.title+"___"+secondItem}>{ReactHtmlParser(row[secondItem])}</td>
                </tr>
            );
        } else {
            const firstItem = keys.shift();
            const awardNo = (this.props.tableNum === 3) ? row[keys[0]] : "";
            const lookupResults = this.props.lookupResults;
            const firstObject = this.makeReact(row[firstItem]);
            if (this.props.tableNum === "lookup") {
                return (
                    <tr key={this.props.title} email={row['email']} record={row['record']} pid={row['pid']} recordinstance={row['recordInstance']} className={classNames}>
                        <th key={this.props.title+"___"+firstItem}>{this.addInputs(firstObject, firstItem, row['pid'], row['record'], row['recordInstance'], lookupResults, awardNo)} {deleteRowX}</th>
                        {keys.map(item => {
                            const rowkey = this.props.title+"___"+item;
                            const currentObject = this.makeReact(row[item] ?? "");
                            return <td key={rowkey}>{this.addInputs(currentObject, item, row['pid'], row['record'], row['recordInstance'], lookupResults, awardNo)}</td>
                        })}
                        <td key={this.props.title+"___addRow"}>
                            <button onClick={this.props.addRow.bind(null, row)}>Add Row</button>
                        </td>
                    </tr>
                );
            }
            const email = NIHTable.getEmailFromRow(row, headers[0]);
            const pid = row['pid'];
            const recordId = (row['record'] === MainGroup.getNotAvailableText()) ? "" : row['record'];
            const recordInstance = row['recordInstance'];
            return (
                <tr key={this.props.title} email={email} record={row['record']} pid={row['pid']} recordinstance={row['recordInstance']} className={classNames}>
                    <th key={this.props.title+"___"+firstItem}>{this.addInputs(firstObject, firstItem, pid, recordId, recordInstance, lookupResults, awardNo)} {deleteRowX}</th>
                    {keys.map(item => {
                        const rowkey = this.props.title+"___"+item;
                        const className = this.getClassName(row[item]);
                        const currentObject = this.makeReact(row[item]);
                        return <td key={rowkey} className={className}>{this.addInputs(currentObject, item, pid, recordId, recordInstance, lookupResults, awardNo)}</td>
                    })}
                </tr>
            );
        }
    }
}