import TableRow from './TableRow.js';
import NIHTable from './NIHTable.js';
import React, {Component} from "react";

export default class TableBody extends Component {
    makeSummaryRow(headers, tableData, tableNum) {
        if (tableNum === 4) {
            const facultyCol = headers[0];
            const directDollarsCol = headers[headers.length - 1];
            const numFaculty = this.getNumFaculty(tableData, facultyCol);
            if (numFaculty > 0) {
                const totalDollars = this.getTotalDollars(tableData, directDollarsCol);
                const avgDollars = this.prettyMoney(Math.round(totalDollars / numFaculty));
                const numTitleCols = headers.length - 1;
                const headerClasses = "bolded greybg left-align";
                const moneyClasses = "alignright";
                const totalTitle = "total_title";
                const totalAmount = "total_amount";
                return <tr>
                    <td key={totalTitle} className={headerClasses} colSpan={numTitleCols}>Average Grant<br/>Support per<br/>Participating Faculty<br/>Member</td>
                    <td key={totalAmount} className={moneyClasses}>{avgDollars}</td>
                </tr>;
            }
        }
        return <tr></tr>;
    }

    getTotalDollars(data, col) {
        let total = 0;
        for (let i=0; i < data.length; i++) {
            const amt = this.unprettyMoney(data[i][col]);
            if (!isNaN(amt)) {
                total += parseInt(amt);
            }
        }
        return total;
    }

    pretty(num) {
        if (isNaN(num)) {
            return "";
        }
        
        let n = num; 
        const chunks = [];
        const segment = 1000;
        const separator = ",";
        const wholeNumber = Math.floor(n);
        const decimal = num - wholeNumber;
        while (Math.floor(n) > 0) {
            const chunk = n % segment;
            n = Math.floor(n / segment);
            if (n > 0) {
                chunks.unshift(this.pad(chunk, 3));
            } else {
                chunks.unshift(chunk);
            }
        }
        if (chunks.length > 0) {
            const prettyInt = chunks.join(separator);
            if (num > wholeNumber) {
                return prettyInt+"."+decimal;
            } else {
                return prettyInt;
            }
        } else {
            return "0";
        }
    }

    pad(num, digits) {
        let i;
        if (num === 0) {
            let n = "";
            for (i=0; i < digits; i++) {
                n = "0"+n;
            }
            return n;
        }

        let n = num.toString();
        for (i=1; i < digits; i++) {
            const place = Math.pow(10, i);
            if (n < place) {
                n = "0"+n;
            }
        }
        return n;
    }

    prettyMoney(amt) {
        return '$'+this.pretty(amt);
    }

    unprettyMoney(figure) {
        if (!figure) {
            return 0;
        }
        return figure
            .replace(/,/g, "")
            .replace(/\$/, "");
    }

    getNumFaculty(data, facultyCol) {
        const faculty = [];
        for (let i=0; i < data.length; i++) {
            const currName = data[i][facultyCol];
            if (faculty.indexOf(currName) < 0) {
                faculty.push(currName);
            }
        }
        return faculty.length;
    }
    
    render() {
        const headers = this.props.headers;
        const tableData = this.props.data;
        const link = this.props.link;
        const tableNum = this.props.tableNum;
        const lookupResults = this.props.lookupResults;
        const emptyTableMssg = this.props.emptyTableMessage;
        if (tableData.length === 0) {
            const numCols = headers.length;
            return (
                <tbody>
                    <tr>
                        <td colSpan={numCols}>{emptyTableMssg}</td>
                    </tr>
                </tbody>
            );
        } else {
            const summaryRow = this.makeSummaryRow(headers, tableData, tableNum);
            return (<tbody>
                {tableData.map(row => {
                    const keyValue = NIHTable.makeRowTitle(row, headers, tableNum)
                    return <TableRow
                        lookupResults={lookupResults}
                        key={keyValue}
                        tableNum={tableNum}
                        title={keyValue}
                        row={row}
                        link={link}
                        headers={headers}
                        addRow={this.props.addRow}
                        deleteRow={this.props.deleteRow}
                        updateCSV={this.props.updateCSV}
                    />
                })}
                {summaryRow}
            </tbody>);

        }
    }
}