import React, {Component} from "react";

export default class GrantChooseButton extends Component {
    render() {
        const paragraphKey = this.props.key+"___paragraph";
        return (
            <section>
                <p key={paragraphKey}>
                    <button key={this.props.buttonKey} onClick={this.props.onClickAction.bind(this, this.props.title, this.props.number, this.props.pi)}>Choose Grant</button>
                </p>
            </section>
        );
    }
}