import React, {Component} from "react";
import GrantChooseButton from "./GrantChooseButton.js";
import PlainTableBody from "./PlainTableBody.js";
import TableHeaders from './TableHeaders.js';

export default class GrantChooseTable extends Component {
    makeCells() {
        const newData = [];
        for (let i=0; i < this.props.tableData.length; i++) {
            const row = this.props.tableData[i];
            const newRow = [];
            for (let j=0; j < this.props.headers.length; j++) {
                const header = this.props.headers[j];
                newRow.push(row[header] ?? "");
            }
            const buttonKey = "grantbutton___"+i;
            newRow.push(<GrantChooseButton
                buttonKey={buttonKey}
                title={row['Grant Title']}
                number={row['Award Number']}
                pi={row['PD/PI']}
                onClickAction={this.props.onSelect}
            />);
            newData.push(newRow);
        }
        return newData;
    }

    render() {
        const title = 'lookupGrantTable';
        const tableData = this.makeCells();
        const tableKey = title+"___table";

        return (
            <table className='smaller' key={tableKey}>
            <TableHeaders
                headers={this.props.headers}
                dateOfReport={this.props.dateOfReport}
            />
            <PlainTableBody
                data={tableData}
                title={title}
            />
        </table>);

    }
}